<template>
  <span class="i-component__fragment">
    <el-dropdown>
      <el-button
        :size="size"
        :type="type"
        :plain="plain"
        :round="round"
        :circle="circle"
      >
        {{ text }}
        <i-icon :path="`${path}/icon`" size="12" icon="ArrowDown" />
      </el-button>
      <template #dropdown>
        <el-dropdown-menu>
          <el-dropdown-item
            v-for="(item, index) in body"
            :key="index"
            :command="index"
            :disabled="item.disabled"
            :divided="item.divided"
          >
            <i-component
              v-if="actionItems.includes(item.actionType)"
              :path="`/${path}/${index}/${item.renderer}`"
              :renderer="item.renderer"
              :props="iAttrs(item)"
              :init-data="state.data"
              classname="i-dropdown__item"
            />
          </el-dropdown-item>
        </el-dropdown-menu>
      </template>
    </el-dropdown>
  </span>
</template>

<script>
import { defineComponent } from 'vue';

import useDerivedProps from '../../hooks/useDerivedProps';
import useInitData from '../../hooks/useInitData';
import initData from '../props/init-data';
import mixinProps from '../props/dropdown';

const actionItems = ['ajax', 'copy', 'redirect', 'url'];

export default defineComponent({
  name: 'Dropdown',
  mixins: [mixinProps, initData],
  setup(props, ctx) {
    const state = useInitData(props);
    return {
      path: ctx.attrs.path,
      actionItems,
      ...useDerivedProps(props),
      state,
    };
  },
});
</script>
