<template>
  <span v-loading="loading" class="i-quickedit__container">
    <el-popover
      :title="title"
      ref="quickedit"
      width="305px"
      trigger="click"
      popper-class="i-quickedit__container"
    >
      <template #reference>
        <span class="el-link el-link--info is-underline">
          <span class="el-link--inner">
            {{ renderText }}
            <i-icon :path="`${path}/edit`" size="14" icon="Edit"/>
          </span>
        </span>
      </template>
      <el-form
        ref="quickeditor"
        class="i-quickedit__form"
        inline
        :model="formdata"
      >
        <el-form-item prop="keyword" :rules="iRules">
          <el-input v-model="formdata.keyword" clearable />
        </el-form-item>
        <el-form-item>
          <el-button type="primary" plain @click="onSubmit">
            
          </el-button>
        </el-form-item>
      </el-form>
    </el-popover>
  </span>
</template>

<script>
import {computed, defineComponent, reactive, getCurrentInstance, watch, ref} from 'vue';

import useInitData from '../../../hooks/useInitData';
import initData from '../../props/init-data';
import mixinProps from '../../props/form/quickedit';

export default defineComponent({
  name: 'Quickedit',
  mixins: [mixinProps, initData],
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const state = useInitData(props);
    const loading = ref(false);
    const renderText = computed(() => proxy.$renderTpl(props.label, state.data));
    const formdata = reactive({
      keyword: state.data[props.keyword] || '',
    });
    const iRules = props.rules.map(item => {
      const exp = item.exp;
      const validate = {};
      validate.trigger = 'blur';
      validate.validator = (rule, value, callback) => {
        if (proxy.$isString(exp)) {
          const isValid = proxy.$expressionEval(exp, {[props.keyword]:formdata.keyword});
          if (isValid) {
            callback(new Error(item.message));
          } else {
            callback();
          }
        }
      }
      return validate;
    });

    const onSubmit = () => {
      proxy.$refs.quickeditor.validate((valid) => {
        if (valid) {
          const { actionApi } = props;
          const { ...other } = ctx.attrs;
          let context = {};
          loading.value = true;
          actionApi.method = 'put';
          context[props.keyword] = formdata.keyword;
          context = { ...state.data, ...context };

          props.action(
            {
              actionType: 'ajax',
              actionApi,
              ...other,
            },
            context,
            handleAfterAction,
          );
        } else {
          proxy.$notice({
            title: '',
            message: '',
            type: 'error',
          });
          return false;
        }
      });
    };
    const handleAfterAction = data => {
      loading.value = false;
      if (props.reload) {
        proxy.$eventHub.$emit('component:reload', props.reload, data);
      }
      proxy.$refs.quickedit.tooltipRef.onClose();
    };
    watch(() => state, () => {
      formdata.keyword = state.data[props.keyword];
    }, {
      deep: true,
    });

    return {
      path: ctx.attrs.path,
      onSubmit,
      state,
      loading,
      iRules,
      renderText,
      formdata,
    };
  },
});
</script>
