<template>
  <div :class="['i-each__container', classname]">
    <div
      v-for="(dataItem, index) in state.data"
      :key="`${path}/${index}`"
      :track="`${path}/each`"
      renderer="each"
      class="i-each__container__item"
    >
      <template v-if="$isArray(body)">
        <template
          v-for="(item, count) in body"
          :key="`${path}/${index}/${item.renderer}/${count}`"
        >
          <i-component
            :renderer="item.renderer"
            :path="`${path}/body/${index}/${item.renderer}`"
            :props="iAttrs(item)"
            :header="iHeader(item)"
            :body="iBody(item)"
            :footer="iFooter(item)"
            :init-data="dataItem"
          />
        </template>
      </template>
      <i-component
        v-else
        :props="iAttrs(body)"
        :renderer="body.renderer"
        :path="`${path}/body/${body.renderer}`"
        :header="iHeader(body)"
        :body="iBody(body)"
        :footer="iFooter(body)"
        :init-data="dataItem"
      />
    </div>
  </div>
</template>

<script>
import { defineComponent } from 'vue';

import useInitData from '../../hooks/useInitData';
import useDerivedProps from '../../hooks/useDerivedProps';
import initData from '../props/init-data';
import mixinProps from '../props/each';

export default defineComponent({
  name: 'Each',
  mixins: [mixinProps, initData],
  setup(props, ctx) {
    const state = useInitData(props);

    return {
      path: ctx.attrs.path,
      ...useDerivedProps(props),
      state,
    };
  },
});
</script>
