<template>
  <el-timeline
    v-if="iVisible"
    v-show="iHidden"
    :reverse="state.data.reverse"
    class="i-timeline__container"
  >
    <el-timeline-item
      v-for="(item, index) in state.data"
      :key="index"
      :timestamp="item[value]"
      :placement="placement"
      :size="size"
      :hollow="hollow"
      :type="getMapStatus(item)"
    >
      <template v-if="$isArray(body)">
        <template
          v-for="(child, count) in body"
          :key="`${path}/${index}/${child.renderer}/${count}`"
        >
          <i-component
            :renderer="child.renderer"
            :path="`${path}/body/${index}/${child.renderer}`"
            :props="iAttrs(child)"
            :header="iHeader(child)"
            :body="iBody(child)"
            :footer="iFooter(child)"
            :init-data="item"
          />
        </template>
      </template>
      <i-component
        v-else-if="$isObject(body)"
        :renderer="body.renderer"
        :props="iAttrs(body)"
        :path="`${path}/body/${body.renderer}`"
        :header="iHeader(body)"
        :body="iBody(body)"
        :footer="iFooter(body)"
        :init-data="item"
      />
      <template v-else-if="$isString(body)">
        {{$renderTpl(body, state.data)}}
      </template>
    </el-timeline-item>
  </el-timeline>
</template>

<script>
import {defineComponent, getCurrentInstance} from 'vue';

import useDerivedProps from '../../hooks/useDerivedProps';
import useInitData from '../../hooks/useInitData';
import useVisible from '../../hooks/useVisible';
import useLinkage from '../../hooks/useLinkage';
import statusMap from '../../hooks/useStatusMap';
import visible from '../props/visible';
import initData from '../props/init-data';
import mixinProps from '../props/timeline';

export default defineComponent({
  name: 'Timeline',
  mixins: [mixinProps, visible, initData],
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const state = useInitData(props);
    const getMapStatus = (context) => {
      if (props.type) {
        const key = proxy.$renderTpl(props.type, context);
        return statusMap[key];
      }
      return 'primary';
    };
    return {
      path: ctx.attrs.path,
      ...useDerivedProps(props),
      ...useVisible(props),
      ...useLinkage(props),
      getMapStatus,
      state,
    };
  },
});
</script>
