import {config as GLOBAL_CONFIG} from './config';
const scripts = [];
const links = [];

const appendStyle = (assetName) => {
  const asset = GLOBAL_CONFIG.assets[assetName].link;
  const link = document.createElement('link');
  const name = asset.replace(/(.*\/)*([^.]+).*/ig,'$2');
  if (!links.includes(name)) {
    link.id = `${name}_${new Date().getTime()}`;
    link.href = asset;
    link.type = 'text/css';
    link.rel = 'stylesheet';
    links.push(name);
    document.head.appendChild(link);
  }
};

const appendScript = (assetName) => {
  const asset = GLOBAL_CONFIG.assets[assetName].script;
  return new Promise(function(resolve, reject) {
    if (!asset) {
      return reject({message: `${assetName}`});
    }
    const script = document.createElement('script')
    const name = asset.replace(/(.*\/)*([^.]+).*/ig,'$2');
    if (scripts.includes(name)) {
      return resolve();
    }
    scripts.push(name);
    script.id = `${name}_${new Date().getTime()}`;
    script.src = asset;

    script.onload = script.onreadystatechange = function() {
      if (
        !this.readyState ||
        this.readyState === 'load' ||
        this.readyState === 'complete'
      ) {
        resolve()
      } else {
        reject()
      }

      script.onload = script.onreadystatechange = null
      script.onerror = function() {
        reject()
      }
    }
    document.body.appendChild(script)
  });
};

export {appendStyle, appendScript};
