import {getCurrentInstance} from 'vue';
import {appendScript, appendStyle} from '../utils/appendAssets';

export default function (props = {}) {
  const { proxy } = getCurrentInstance();
  let instance;
  let steps = [];

  const initDriver = () => {
    if (props.steps && !proxy.$iRenderConfig.editable) {
      appendStyle('driver');
      createSteps().then(() => onDriverStart());
    }
  };
  const createSteps = () => new Promise((resolve) => {
    props.steps.forEach(item => {
      proxy.$eventHub.$emit('component:track', item.element, path => {
        steps.push({
          element: `[path='${path}']`,
          popover: item.popover
        });
        if (steps.length === props.steps.length) {
          resolve();
        }
      });
    });
  });
  const onDriverStart = async() => {
    event.preventDefault();
    event.stopPropagation();
    if (instance) {
      return instance.start();
    }
    appendScript('driver')
      .then(() => {
        instance = new window.Driver({
          doneBtnText: '',
          closeBtnText: '',
          nextBtnText: '',
          prevBtnText: ''
        });
        instance.defineSteps(steps);
        instance.start();
      })
      .catch(e => {
        proxy.$message({
          message: e.message,
          showClose: true,
          grouping: true,
          type: 'error',
        });
        console.error(e);
      });
  };

  return {
    initDriver
  };
}
