<template>
  <el-row
    :gutter="gutter"
    :type="type"
    :justify="justify"
    :align="align"
    :tag="tag"
    :class="['i-grid__container', classname]"
  >
    <template v-if="body&&body.length">
      <el-col
        v-for="(child, index) in body"
        :key="`${path}/${index}/${child.renderer}`"
        :class="classname"
        :span="getSpan(index)"
      >
        <i-component
          :renderer="child.renderer"
          :path="`${path}/body/${index}/${child.renderer}`"
          :header="iHeader(child)"
          :body="iBody(child)"
          :footer="iFooter(child)"
          :props="iAttrs(child)"
          :init-data="state.data"
        />
      </el-col>
    </template>
  </el-row>
</template>
<script>
import { defineComponent } from 'vue';

import useDerivedProps from '../../hooks/useDerivedProps';
import useInitData from '../../hooks/useInitData';
import initData from '../props/init-data';
import mixinProps from '../props/grid';

export default defineComponent({
  name: 'Grid',
  mixins: [mixinProps, initData],
  setup(props, ctx) {
    const state = useInitData(props);
    const spanList = (String(props.span || '')).split(',').map(item => Number(item));
    const getSpan = (index) => {
      const target = index % spanList.length;
      return spanList[target];
    };
    return {
      path: ctx.attrs.path,
      ...useDerivedProps(props),
      spanList,
      getSpan,
      state,
    };
  },
});
</script>
