<template>
  <el-tabs
    v-if="iVisible"
    v-show="iHidden"
    v-model="state.data.activeIndex"
    :path="`${path}`"
    :type="type"
    :style="tabStyle"
    :stretch="stretch"
    :tab-position="tabPosition"
    class="i-tabs__container"
  >
    <template v-for="(item, index) in panels">
      <el-tab-pane
        :key="`${path}/${index}`"
        :label="item.label"
        :name="`${index + 1}`"
        :lazy="true"
        v-bind="iAttrs(item)"
        v-if="showPanel(item)"
      >
        <template #label>
          <i-icon v-if="item.icon" :path="`${path}/icon`" :icon="item.icon" />
          {{ item.label }}
        </template>
        <i-component
          v-if="$isObject(item.body)"
          :renderer="item.body.renderer"
          :path="`${path}/panels/${index}/${item.body.renderer}`"
          :track="`${path}/panels/${index}/body`"
          :props="iAttrs(item.body)"
          :header="iHeader(item.body)"
          :body="iBody(item.body)"
          :footer="iFooter(item.body)"
          :init-data="state.data"
        />
        <template v-else>
          <template
            v-for="(child, count) in item.body"
            :key="`${path}/${count}/${child.renderer}`"
          >
            <i-component
              :renderer="child.renderer"
              :path="`${path}/panels/${index}/body/${count}/${child.renderer}`"
              :track="`${path}/panels/${index}/body/${count}`"
              :props="iAttrs(child)"
              :header="iHeader(child)"
              :body="iBody(child)"
              :footer="iFooter(child)"
              :init-data="state.data"
            />
          </template>
        </template>
      </el-tab-pane>
    </template>
  </el-tabs>
</template>
<script>
import {defineComponent, watch, getCurrentInstance} from 'vue';

import useDerivedProps from '../../hooks/useDerivedProps';
import useInitData from '../../hooks/useInitData';
import useVisible from '../../hooks/useVisible';
import visible from '../props/visible';
import initData from '../props/init-data';
import mixinProps from '../props/tabs';

export default defineComponent({
  name: 'Tabs',
  mixins: [mixinProps, visible, initData],
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const state = useInitData(props);
    const showPanel = (panel = {}) => {
      let visible = true;
      if (panel.visibleOn) {
        visible = proxy.$expressionEval(panel.visibleOn, proxy.state.data);
      }
      return visible;
    };
    state.data.activeIndex = `${props.initData.activeIndex || props.activeIndex}`;

    watch(() => props.activeIndex, val => {
      state.data.activeIndex = `${val || 1}`;
    }, {
      immediate: true
    });

    return {
      path: ctx.attrs.path,
      ...useDerivedProps(),
      ...useVisible(props),
      showPanel,
      state,
    };
  },
});
</script>
