<template>
  <el-radio-group
    v-model="iModelValue[name]"
    :name="name"
  >
    <component
      :is="type === 'button'? 'ElRadioButton': 'ElRadio'"
      v-for="(option, index) in options"
      :key="index"
      :label="option.value"
      :disabled="disabledOn(option)"
    >
      {{ option.text }}
    </component>
  </el-radio-group>
</template>

<script>
import {defineComponent, watch, reactive, getCurrentInstance} from 'vue';

import useInitData from '../../../hooks/useInitData';
import initData from '../../props/init-data';
import mixinProps from '../../props/form/radio';

export default defineComponent({
  name: 'Radio',
  mixins: [mixinProps, initData],
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const state = useInitData(props);
    const iModelValue = reactive({});
    let firstCheckable;
    const disabledOn = (option) => {
      let disabled = false;
      if (option.disabledOn) {
        disabled = proxy.$expressionEval(option.disabledOn, proxy.state.data);
      }
      if (option.disabledOn && !disabled && !firstCheckable) {
        firstCheckable = option.value;
        iModelValue[props.name] = firstCheckable;
      }
      return disabled;
    };
    watch(() => props.defaultValue, (val) => {
      iModelValue[props.name] = val;
    }, {
      immediate: true,
    });

    watch(() => props.modelValue, (val) => {
      iModelValue[props.name] = val;
    }, {
      immediate: true,
    });

    watch(() => iModelValue[props.name], (val) => {
      ctx.emit('update:modelValue', val);
      if (props.target) {
        props.linkageTrigger(props.target, iModelValue);
      }
    });

    return {
      path: ctx.attrs.path,
      iModelValue,
      disabledOn,
      state,
    };
  },
});
</script>
