import {printPartial} from './tpl';
import {appendScript} from './appendAssets';
import {config as assetsConfig} from './config';

const actions = {
  'linkage': function(proxy, props, config, context, onActionFeedback) {
    onActionFeedback&&onActionFeedback('CANCEL_LOADING');
  },
  'ajax': function (proxy, props, config, context, onActionFeedback) {
    proxy.handleFetchApi(config.actionApi, context)
      .then(res => {
        onActionFeedback&&onActionFeedback(res);
        this['reload'](proxy, props, config, res);
      })
      .catch(error => {
        onActionFeedback&&onActionFeedback('CANCEL_LOADING');
      });
  },
  'url': function(proxy, props, config, context, onActionFeedback) {
    const url = proxy.$compiledUrl(config.url, context);

    if (/^https?:\/\//.test(url)) {
      config.blank ? window.open(url) : (window.location.href = url);
    } else {
      const params = proxy.$compiledParams(config.params, context);
      proxy.$router.push({
        path: url,
        query: params
      });
    }
    onActionFeedback&&onActionFeedback('CANCEL_LOADING');
  },
  'copy': function (proxy, props, config, context, onActionFeedback) {
    if (!config.content) {
      throw new Error('content!');
    }
    const content = proxy.$compiledUrl(config.content, context);
    appendScript(assetsConfig.assets.clipboard.script)
      .then(() => {
        window.ClipboardJS.copy(content);
        proxy.$message.success('');
      })
      .catch(e => {
        proxy.$message({
          message: e.message,
          showClose: true,
          grouping: true,
          type: 'error',
        });
        console.error(e);
      })
      .finally(() => {
        onActionFeedback&&onActionFeedback('CANCEL_LOADING');
      });
  },
  'dialog': function(proxy, props, config, context, onActionFeedback) {
    proxy.$eventHub.$emit('portal:create', config.path, {
      body: config.body,
      data: context,
      actionType: config.actionType,
      visible: true,
    });
    onActionFeedback&&onActionFeedback('CANCEL_LOADING');
  },
  'drawer': function (proxy, props, config, context, feedback) {
    this['dialog'](proxy, props, config, context, feedback);
  },
  'print': function(proxy, props, config, context, onActionFeedback) {
    try {
      if (!config.target) {
        throw new Error('');
      }
      const timer = setTimeout(() => {
        proxy.$message.error('');
      }, 3000);
      proxy.$eventHub.$emit('component:track', config.target, path => {
        clearTimeout(timer);
        const printDom = document.querySelectorAll(`[path='${path}']`)[0];
        printPartial(printDom);
      });
    } catch (e) {
      proxy.$message({
        message: e.message,
        showClose: true,
        grouping: true,
        type: 'error',
      });
      console.error(e);
    } finally {
      onActionFeedback&&onActionFeedback('CANCEL_LOADING');
    }
  },
  'fullscreen': function (proxy, props, config, context, onActionFeedback) {
    const ele = config.target || document.body;
    const isFull = !!(document.fullscreen || document.mozFullScreen || document.webkitIsFullScreen || document.webkitFullScreen || document.msFullScreen);

    if (isFull) {
      if (document.exitFullScreen) {
        document.exitFullScreen();
      } else if (document.mozCancelFullScreen) {
        document.mozCancelFullScreen();
      } else if (document.webkitExitFullscreen) {
        document.webkitExitFullscreen();
      } else if (document.msExitFullscreen) {
        document.msExitFullscreen();
      }
    } else {
      proxy.$message.success('');
      if (ele.requestFullscreen) {
        ele.requestFullscreen();
      } else if (ele.mozRequestFullScreen) {
        ele.mozRequestFullScreen();
      } else if (ele.webkitRequestFullscreen) {
        ele.webkitRequestFullscreen();
      } else if (ele.msRequestFullscreen) {
        ele.msRequestFullscreen();
      }
    }
    onActionFeedback&&onActionFeedback('CANCEL_LOADING');
  },
  'reload': function (proxy, props, config, context) {
    const { reload } = config;
    if (reload) {
      proxy.$eventHub.$emit('component:reload', reload, context);
    }
  },
  'mail': function(proxy, props, config, context, onActionFeedback) {
    const timer = setTimeout(() => {
      window.location.href = `mailto:?subject=&cc=&body=`;
      onActionFeedback&&onActionFeedback('CANCEL_LOADING');
      clearTimeout(timer);
    }, 1500);
  }
};

const dispatchAction = (proxy, props, config, context, feedback) => {
  if (!actions[config.actionType] && !['submit', 'reset'].includes(config.actionType)) {
    return;
  }
  if (
      (config.renderer.includes('action') && config.actions) ||
      (config.renderer.includes('chart') && config.footer.actionType)
  ) {
    if (config && ['submit', 'reset'].includes(config.actionType)) {
      return props.action(feedback);
    }
    return actions[config.actionType](proxy, props, config, context, feedback);
  }
  if (['submit', 'reset'].includes(config.actionType)) {
    return props.action(feedback);
  }
  return actions[config.actionType](proxy, props, config, context, feedback);
};

export {
  dispatchAction
};
