import {template} from 'lodash';
import {v4 as uuidv4} from 'uuid';
import {appendScript} from './appendAssets';

const isArray = obj => Object.prototype.toString.call(obj) === '[object Array]';
const isObject = obj => Object.prototype.toString.call(obj) === '[object Object]';
const isString = obj => Object.prototype.toString.call(obj) === '[object String]';

const renderTpl = (tpl, data = {}) => {
  const compiled = template(tpl);

  return compiled({ data });
};

const compiledUrl = (tpl, data = {}) => tpl.replace(/\${([^}{]+?)}/gi, (escape, name) => data[name] || '');

const compiledKey = (tpl, data = {}) => tpl.replace(/\${([^}{]+?)}/gi, (escape, name) => name);

const compiledParams = (params, data = {}) => {
  let compiledParams = {};

  if (params) {
    const compiled = template(params);
    compiledParams = compiled(data).replace(/\n/g, '\\n').replace(/\r/g, '\\r');
    compiledParams = JSON.parse(compiledParams);
  }

  return compiledParams;
};

const expressionEval = (expression, data, ctx) => {
  if (expression) {
    // eslint-disable-next-line no-new-func
    const fn = new Function('data', `with(data) { return (${expression}) }`);
    try {
      return fn.call(data, data);
    } catch (e) {
      ctx && ctx.$message({
        message: e.message,
        showClose: true,
        grouping: true,
        type: 'error'
      });
      console.error(e);
    }
  }
};

const json2FormData = (isFormData, data) => {
  let formData;
  if (isFormData) {
    formData = new FormData();
    for (const name in data) {
      if (Object.prototype.hasOwnProperty.call(data, name)) {
        formData.append(name, data[name]);
      }
    }
  } else {
    formData = {};
    for (const name in data) {
      if (Object.prototype.hasOwnProperty.call(data, name)) {
        formData[name] = data[name];
      }
    }
  }
  return formData;
};

const saveInitFormType = (ctx, isFormData) => {
  ctx.$iRenderConfig.isFormData = isFormData;
};

const saveAdaptor = (ctx, script) => {
  return new Promise((resolve, reject) => {
    ctx.$iRenderConfig.adaptor = script;
    ctx.$iRenderConfig.isAdaptorChanged = true;
    resolve();
  });
};

const saveInitStyle = (ctx, style) => {
  return new Promise((resolve, reject) => {
    try {
      let ele = document.getElementById('i-renderer-setting__style');

      if (!ele) {
        ele = document.createElement('style');
        ele.id = 'i-renderer-setting__style';
        document.head.appendChild(ele);
      }
      ele.innerHTML = style;
      resolve();
    } catch (e) {
      reject(e);
    }
  });
};

const initSetting = (ctx, iRenderConfig) => {
  const {
    isFormData,
    style,
    adaptor,
  } = iRenderConfig;
  return Promise.all([saveAdaptor(ctx, adaptor), saveInitStyle(ctx, style), saveInitFormType(ctx, isFormData)])
    .then(() => {
      ctx.$message({
        showClose: true,
        message: '',
        type: 'success',
      });
    }).catch(error => {
      ctx.$message({
        showClose: true,
        message: error,
        type: 'error',
      });
    });
};

const purifyObject = (data, mixins) => {
  const mixinsProps = mixins.reduce((total, item) => {
    Object.assign(total, item.props);
    return total;
  }, {});
  for (const key in data) {
    if (
      data.hasOwnProperty(key) &&
      (
        (data[key] === '') ||
        (
          mixinsProps[key] &&
          mixinsProps[key].hasOwnProperty('default') &&
          data[key] === mixinsProps[key].default
        )
      )
    ) {
      delete data[key];
    }
  }
};

const printPartial = target => {
  const iframe = document.createElement('iframe');
  const html = document.createElement('div');
  const body = document.createElement('body');
  iframe.setAttribute('style', 'position: absolute;width:0;height:0;top:-10px;left:-10px;');
  body.appendChild(target.cloneNode(true));
  html.appendChild(document.head.cloneNode(true));
  html.appendChild(body);
  document.body.appendChild(iframe);
  iframe.contentWindow.document.write('<!doctype html>');
  iframe.contentWindow.document.write(html.innerHTML);
  iframe.contentWindow.document.close();
  iframe.contentWindow.focus();
  iframe.onload = () => {
    iframe.contentWindow.print();
    document.body.removeChild(iframe);
  };
};

const appendPageStyle = (innerStyle) => new Promise(resolve => {
  import(/*webpackChunkName: "less", webpackPrefetch: true */ 'less').then(res => {
    res.default.render(innerStyle).then(res => {
      const style = document.createElement('style');
      const id = uuidv4();
      style.type = 'text/css';
      style.id = id;
      try {
        style.appendChild(document.createTextNode(res.css));
      } catch (ex) {
        style.styleSheet.cssText = res.css;
      }
      document.head.appendChild(style);
      resolve(id);
    });
  }).catch(e => {
    console.error(e);
  });
});

const removePageStyle = (id) => {
  const style = document.getElementById(id);
  style.parentNode.removeChild(style);
};

const appendWatermark = (proxy, text) => new Promise(resolve => {
  appendScript('watermark')
    .then(() => {
      const watermarkDom = new window.webWatermark.Watermark({text});
      watermarkDom.render();
      resolve(watermarkDom);
    }).catch(e => {
      proxy.$message({
        message: e.message,
        showClose: true,
        grouping: true,
        type: 'error',
      });
      console.error(e);
    });
});

export {
  isString,
  isObject,
  isArray,
  renderTpl,
  compiledKey,
  compiledUrl,
  compiledParams,
  expressionEval,
  json2FormData,
  saveInitStyle,
  saveInitFormType,
  saveAdaptor,
  initSetting,
  purifyObject,
  printPartial,
  appendPageStyle,
  removePageStyle,
  appendWatermark
};
