<template>
  <el-calendar
    v-model="iModelValue[name]"
    class="i-form__calendar"
    ref="calendar"
  >
    <template #header="{ date }">
      <span>{{ date }}</span>
      <el-button-group>
        <el-button size="small" @click="selectDate('prev-month')"></el-button>
        <el-button size="small" @click="selectDate('today')"></el-button>
        <el-button size="small" @click="selectDate('next-month')"></el-button>
      </el-button-group>
    </template>
    <template
      v-if="body"
      #dateCell="{data}"
    >
      <div
        class="i-form__calendar__body"
        :class="data.isSelected ? 'selected' : ''"
      >
        <div class="i-form-calendar__body__index">
          {{ getDay(data) }}
        </div>
        <template v-if="$isArray(body)">
          <template
            v-for="(child, index) in body"
            :key="`${path}/${data.day}/${child.renderer}`"
          >
            <i-component
              v-bind="iAttrs(child)"
              :renderer="child.renderer"
              :track="`${path}/body/${index}/${child.renderer}`"
              :path="`${path}/body/${getDay(data)}/${child.renderer}`"
              :props="iAttrs(child)"
              :header="iHeader(child)"
              :body="iBody(child)"
              :footer="iFooter(child)"
              :init-data="calendarData[data.day]"
            />
          </template>
        </template>
        <i-component
          v-else-if="$isObject(body)"
          v-bind="iAttrs(body)"
          :renderer="body.renderer"
          :path="`${path}/body/${body.renderer}`"
          :props="iAttrs(body)"
          :header="iHeader(body)"
          :body="iBody(body)"
          :footer="iFooter(body)"
          :init-data="calendarData[data.day]"
        />
      </div>
    </template>
  </el-calendar>
</template>
<script>
import {getCurrentInstance, defineComponent, onMounted, reactive, ref, watch, nextTick} from 'vue';

import useLinkage from '../../../hooks/useLinkage';
import useDerivedProps from '../../../hooks/useDerivedProps';
import useInitApi from '../../../hooks/useInitApi';
import useInitData from '../../../hooks/useInitData';
import initApi from '../../props/init-api';
import initData from '../../props/init-data';
import mixinProps from '../../props/form/calendar';

export default defineComponent({
  name: 'Calendar',
  mixins: [mixinProps, initData, initApi],
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const { handleFetchApi } = useInitApi(props);
    const state = useInitData(props);
    const iModelValue = reactive({});
    const calendarData = ref('');
    const fetchDataList = () => {
      const apiData = props.initApi;
      handleFetchApi(apiData, state.data)
        .then((result) => {
          calendarData.value = result;
        });
    };
    const selectDate = (value) => {
      proxy.$refs.calendar.selectDate(value);
      nextTick(() => {
        state.data.curDatePrefix = proxy.$refs.calendar.curMonthDatePrefix;
        fetchDataList();
      });
    }
    const getDay = (data) => {
      const day = data.day.split('-').slice(2)[0];
      if (day.startsWith('0')) {
        return day.charAt(1);
      }
      return day;
    };

    watch(() => props.modelValue, (val) => {
      iModelValue[props.name] = val || new Date().toISOString();
    }, {
      immediate: true,
    });

    watch(() => iModelValue[props.name], (val, old) => {
      ctx.emit('update:modelValue', val);
      if (props.target) {
        props.linkageTrigger(props.target, iModelValue);
      }
    });

    onMounted(() => {
      if (props.initApi) {
        state.data.curDatePrefix = proxy.$refs.calendar.curMonthDatePrefix;
        fetchDataList();
      }
    });

    return {
      path: ctx.attrs.path,
      ...useLinkage(props),
      ...useDerivedProps(props),
      selectDate,
      getDay,
      calendarData,
      iModelValue,
      state,
    };
  }
});
</script>
