<template>
  <div class="i-video__container">
    <video
      :ref="uuid"
      controls
      :width="width"
      :height="height"
      :poster="poster"
      preload="preload"
      class="video-js"
    >
      <source :src="src" :type="type" />
    </video>
  </div>
</template>
<script>
import {defineComponent, onBeforeMount, onMounted, getCurrentInstance, onBeforeUnmount} from 'vue';
import {v4 as uuidv4} from 'uuid';
import {appendScript, appendStyle} from '../../utils/appendAssets';
import useDerivedProps from '../../hooks/useDerivedProps';
import useInitData from '../../hooks/useInitData';
import initData from '../props/init-data';
import mixinProps from '../props/video';

export default defineComponent({
  name: 'Video',
  mixins: [mixinProps, initData],
  setup(props) {
    const { proxy } = getCurrentInstance();
    const state = useInitData(props);
    const uuid = uuidv4();
    let player;
    onBeforeMount(() => {
      appendStyle('video');
    });
    onMounted(() => {
      appendScript('video')
        .then(() => {
          player = window.videojs(proxy.$refs[uuid], {
            controls: true,
            autoplay: props.autoplay,
            preload: 'auto'
          }, function onPlayerReady() {
            this.play();
          });
        })
        .catch((e) => {
          proxy.$message({
            message: e.message,
            showClose: true,
            grouping: true,
            type: 'error',
          });
          console.error(e);
        })
        .finally(() => {});
    });
    onBeforeUnmount(() => {
      if (player) {
        player.dispose();
      }
    });
    return {
      ...useDerivedProps(props),
      state,
      uuid
    };
  },
});
</script>
