<template>
  <div class="i-steps__container">
    <el-steps
      :path="path"
      :active="iActive"
      :simple="true"
      :name="name"
      :direction="direction"
      :align-center="alignCenter"
      :finish-status="finishStatus"
      :process-status="processStatus"
    >
      <template
        v-for="(item, index) in panels"
        :key="index"
      >
        <el-step
          :title="item.label"
          :icon="item.icon"
          :status="item.status"
          :description="item.description"
        />
      </template>
    </el-steps>
    <div
      v-loading="!iShow || freshing"
      class="el-steps__main"
    >
      <i-component
        v-if="iShow"
        ref="form"
        renderer="form"
        :index="iActive - 1"
        :path="`${path}/${iActive}`"
        :init-data="state.data"
        :props="{
          renderer: 'form',
          controls: iControls
        }"
      />
    </div>
    <div class="el-steps__footer">
      <el-button-group>
        <el-button
          :disabled="iActive === 1 || loading"
          @click="previous"
        >
          
        </el-button>
        <el-button
          v-if="iActive < panels.length"
          :disabled="loading"
          @click="next"
        >
          
        </el-button>
        <el-button
          v-if="iActive === panels.length"
          v-loading="loading"
          :disabled="loading"
          @click="done"
        >
          
        </el-button>
      </el-button-group>
      <i-component
        v-if="redirect && false"
        ref="redirect"
        renderer="action"
        :path="`${path}/action`"
        :init-data="state.data"
        hidden-on="1 === 1"
        :props="{
          hiddenOn: '1===1',
          renderer: 'action',
          actionType: 'redirect',
          url: redirect,
        }"
      />
    </div>
  </div>
</template>

<script>
import { defineComponent, watch, ref, nextTick, getCurrentInstance } from 'vue';
import useDerivedProps from '../../hooks/useDerivedProps';
import uselinkage from '../../hooks/useLinkage';
import useInitData from '../../hooks/useInitData';
import useInitApi from '../../hooks/useInitApi';
import initData from '../props/init-data';
import initApi from '../props/init-api';
import mixinProps from '../props/steps';
export default defineComponent({
  name: 'Steps',
  mixins: [mixinProps, initData, initApi],
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const state = useInitData(props);
    const { handleFetchApi } = useInitApi(props);
    const iActive = ref(1);
    const iShow = ref(true);
    const iControls = ref([]);
    const loading = ref(false);
    const freshing = ref(false);
    const formdata = {};
    for (const key in props.panels) {
      if (Object.hasOwnProperty.call(props.panels, key)) {
        props.panels[key].body.forEach(control => {
          const renderer = control.renderer;
          const name = control.name;
          const value = control.value || '';
          if (name && proxy.$formItems.includes(renderer) && renderer !== 'action') {
            formdata[name] = value;
          }
        });
      }
    }
    Object.assign(state.data, formdata);
    loading.value = false;
    const previous = () => {
      beforeUpdateActive().then(() => {
        iShow.value = false;
        if (iActive.value > 1) {
          --iActive.value;
        }
        mergeFormData();
        nextTick(() => {
          iShow.value = true;
        });
      }).catch();
    };
    const next = () => {
      beforeUpdateActive().then(() => {
        iShow.value = false;
        if (iActive.value < props.panels.length) {
          ++iActive.value;
        }
        mergeFormData();
        nextTick(() => {
          iShow.value = true;
        });
      }).catch();
    };
    const done = () => {
      beforeUpdateActive(true).then(() => {
        loading.value = true;
        mergeFormData();
        handleFetchApi({
          url: props.actionApi.url,
          method: props.actionApi.method || 'post',
          params: state.data,
        }, () => {
          onAfterSubmit();
        }).finally(() => {
          loading.value = false;
        });
      }).catch();
    };
    const mergeFormData = () => {
      const data = proxy.$refs.form.$refs.component.state.data;
      Object.assign(state.data, data);
    };
    const beforeUpdateActive = (isSubmit) => {
      if (!isSubmit) {
        return Promise.resolve();
      }
      return new Promise((resolve) => {
        proxy.$refs.form.$refs.component.$refs.form.validate((valid) => {
          if (valid) {
            resolve();
          } else {
            proxy.$message({
              message: '',
              grouping: true,
              showClose: true,
              type: 'error',
            });
          }
        });
      });
    };
    const onAfterSubmit = () => {
      if (props.redirect) {
        proxy.$refs.redirect.$refs.component.onClick();
      } else if (props.reload) {
        proxy.$eventHub.$emit('component:reload', props.reload);
      }
    };
    const updateControls = () => {
      freshing.value = true;
      iControls.value = props.panels.reduce((total, current, index) => {
        current.body.forEach(item => {
          item.hiddenOn = `${index} != ${iActive.value - 1}`;
        });
        total = [...total, ...current.body];
        return total;
      }, []);
      const timer = setTimeout(() => {
        freshing.value = false;
        clearTimeout(timer);
      }, 1000);
    };
    watch(() => props.activeIndex, val => {
      iActive.value = val || 1;
    }, {
      immediate: true
    });
    watch(() => iActive.value, () => {
      updateControls();
    }, {
      immediate: true
    });
    return {
      path: ctx.attrs.path,
      ...useDerivedProps(props),
      ...uselinkage(props),
      previous,
      next,
      done,
      iActive,
      iShow,
      iControls,
      loading,
      freshing,
      state
    };
  },
});
</script>
