<script>
import {h, getCurrentInstance} from 'vue';
import useInitData from '../../hooks/useInitData';
import initData from '../props/init-data';
import mixinProps from '../props/render';

export default {
  name: 'Render',
  mixins: [mixinProps, initData],
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const state = useInitData(props);
    let nodeList;
    if (proxy.$isObject(props.body)) {
      nodeList = createNode(props.body);
    } else if (proxy.$isArray(props.body)) {
      nodeList = props.body.map(item => {
        return h(
          item.tag || 'div',
          createNode(item)
        );
      });
    }
    function createNode(node) {
      return {
        class: node.class,
        style: node.style,
        innerHTML: proxy.$renderTpl(node.innerHTML, state.data),
        onClick: () => {
          node.action && props.action(
            {
              path: ctx.attrs.path,
              ...node.action
            },
            state.data,
            () => {},
          )
        }
      };
    }

    return () => h(
      'div',
      nodeList
    );
  }
};
</script>
