<template>
  <div :class="['i-info__container', classname]">
    <div v-if="auto" class="i-info__title" ref="countRef" />
    <div v-else class="i-info__title">
      {{ renderTitle }}
    </div>
    <div v-if="unit" class="i-info__unit">
      {{ unit }}
    </div>
    <el-tooltip v-if="tip" :content="renderTip" placement="top-start">
      <el-icon size="12" class="i-info__tip">
        <component is="info-filled" />
      </el-icon>
    </el-tooltip>
    <div v-if="description" class="i-info__description">
      {{ renderDescription }}
    </div>
  </div>
</template>
<script>
import {defineComponent, computed, getCurrentInstance, onMounted, onBeforeUnmount} from 'vue';
import {appendScript} from '../../utils/appendAssets';
import useInitData from '../../hooks/useInitData';
import initData from '../props/init-data';
import mixinProps from '../props/count';

export default defineComponent({
  name: 'Count',
  mixins: [mixinProps, initData],
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const state = useInitData(props);
    const renderTitle = computed(() => {
      if (props.value) {
        return proxy.$renderTpl(props.value, state.data);
      }
      return undefined;
    });
    const renderTip = computed(() => {
      if (props.tip) {
        return proxy.$renderTpl(props.tip, state.data);
      }
      return undefined;
    });
    const renderDescription = computed(() => {
      if (props.description) {
        return proxy.$renderTpl(props.description, state.data);
      }
      return undefined;
    });
    const startCountUp = () => {
      appendScript('countup')
        .then(() => {
          const value = proxy.$expressionEval(props.value, state.data);
          instance = new window.CountUp(proxy.$refs.countRef, 0, value);
          if (!instance.error) {
            instance.start();
          } else {
            proxy.$message.error(instance.error);
            console.error(instance.error);
          }
        }).catch(e => {
          proxy.$message({
            message: e.message,
            showClose: true,
            grouping: true,
            type: 'error',
          });
          console.error(e);
        });
    };
    let instance;

    onMounted(() => {
      if (props.auto) {
        startCountUp();
      }
    });
    onBeforeUnmount(() => {
      instance&&instance.pauseResume();
    });

    return {
      path: ctx.attrs.path,
      renderTitle,
      renderTip,
      renderDescription,
      state
    };
  }
});
</script>
