<template>
  <el-card
    class="umis-setting__card-margin"
    shadow="hover"
  >
    <template #header>
      <div
        class="umis-setting__header"
      >
        <el-tooltip
          content=""
          placement="top-start"
        >
          <span> <i class="el-icon-info" /> </span>
        </el-tooltip>
        <el-button-group>
          <el-button
            plain
            size="small"
            type="primary"
            @click="onAdd"
          >
            
          </el-button>
          <el-button
            plain
            size="small"
            type="primary"
            @click="onSave"
          >
            
          </el-button>
        </el-button-group>
      </div>
    </template>
    <el-radio-group
      v-model="activeDomain"
      class="umis-setting__api-editor"
    >
      <div
        v-for="(domain, index) in domains"
        :key="index"
        class="umis-setting__api-editor__item"
      >
        <span
          class="umis-setting__api-editor__delete"
          @click="onDel(index)"
        >
          <mis-icon :path="`${path}/multiple/${index}/delete`" class="el-icon-delete" icon="Delete" size="12"/>
        </span>
        <el-input
          v-model="domain.key"
          class="umis-setting__api-editor__label"
        />
        <el-input
          v-model="domain.value"
          class="umis-setting__api-editor__value"
          placeholder=""
        />
        <el-radio
          :label="domain.key"
          class="umis-setting__api-editor__radio"
        >
          {{ '' }}
        </el-radio>
      </div>
    </el-radio-group>
  </el-card>
</template>
<script>
import { getCurrentInstance, defineComponent, ref, onMounted } from 'vue';

export default defineComponent({
  name: 'MisDomain',
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const activeDomain = ref('');
    const domains = ref([]);
    const onAdd = () => {
      domains.value.push({
        key: `UMIS_DOMAIN_DEV_${domains.value.length}`,
        value: ''
      });
    };
    const onDel = (index) => {
      domains.value.splice(index, 1);
    };
    const onSave = () => {
      const domainMap = {};
      let active = '';
      domains.value.forEach(item => {
        if (activeDomain.value === item.key) {
          active = item.value;
        }
        domainMap[item.key] = item.value;
      });
      ctx.emit('input', domainMap);
      proxy.$iRenderConfig.isApiChanged = true;
      proxy.$iRenderConfig.activeDomain = active;
      proxy.$iRenderConfig.domains = domainMap;
      proxy.$message.success('');
    };
    onMounted(() => {
      activeDomain.value = proxy.$iRenderConfig.domains.default;
      proxy.$iRenderConfig.activeDomain = activeDomain.value;
      domains.value = Object.keys(proxy.$iRenderConfig.domains).map(key => {
        return {key, value: proxy.$iRenderConfig.domains[key]};
      });
    });

    return {
      path: ctx.attrs.path,
      onAdd,
      onDel,
      onSave,
      activeDomain,
      domains
    };
  }
});
</script>
