let contextCache = {};
let validCache = {};

const createValid = (control, context, proxy, prop) => {
  if (prop) {
    contextCache[prop] = context;
  }
  control.rules.forEach(function(item) {
    const exp = item.exp;
    if (exp) {
      item.trigger = 'blur';
      item.validator = function(rule, value, callback) {
        const ctx = contextCache[rule.field];
        if (proxy.$isString(exp)) {
          const isValid = proxy.$expressionEval(exp, ctx, proxy);
          if (isValid) {
            callback(new Error(item.message));
          } else {
            callback();
          }
        } else {
          if (validCache[rule.field] && validCache[rule.field].value === value && validCache[rule.field].isValid) {
            return callback();
          }
          proxy.handleFetchApi(exp, ctx).then((result) => {
            validCache[rule.field] = {
              value,
              isValid: result.isValid
            };
            if (result.isValid) {
              callback();
            } else {
              callback(result.message);
            }
          });
        }
      }
    }
  });
};

const clearValid = () => {
  contextCache = {};
  validCache = {};
};

export {createValid, clearValid};
