<template>
  <component
    v-bind="iAttrs(props)"
    :is="componentName"
    ref="component"
    :key="updatedCount"
    :index="index"
    :path="path"
    :track="track || path"
    :classname="props.classname"
    :header="iHeader()"
    :body="iBody()"
    :footer="iFooter()"
    :init-data="iData(props)"
    :error-info="errorInfo"
    :action="filterAction"
    :after-action="afterAction"
  />
</template>
<script>
import {defineComponent, computed, ref, onErrorCaptured, onBeforeMount, onMounted, onBeforeUnmount, getCurrentInstance} from 'vue';
import {dispatchAction} from '../../utils/actions';
import useInitApi from '../../hooks/useInitApi';
import useInitData from '../../hooks/useInitData';
import useDerivedProps from '../../hooks/useDerivedProps';
import initApi from '../props/init-api';
import initData from '../props/init-data';
import mixinProps from '../props/component';

export default defineComponent({
  name: 'Component',
  mixins: [mixinProps, initData, initApi],
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const { handleFetchApi } = useInitApi(props);
    const state = useInitData(props);
    const updatedCount = ref(0);
    const componentName = computed(() => {
      return error.value ? 'i-error' : `i-${props.renderer}`;
    });
    const error = ref('');
    const errorInfo = computed(() => {
      if (error.value) {
        return {
          props: props.props,
          error: error.value,
        };
      }
      return '';
    });
    const filterAction = (config, context, feedback) => {
      return dispatchAction(proxy, props, config, context, feedback);
    };
    const onComponentReload = (target, res) => {
      if (target === 'window') {
        window.location.reload();
      } else if (props && target === props.props.name) {
        let rowIndex = -1;
        if (res && 'i-table, i-cards'.includes(props.renderer) && props.props.updateOnRowId) {
          rowIndex = proxy.$.refs.component.onRowUpdate(res);
        }
        if (rowIndex === -1) {
          updatedCount.value++;
        }
      }
    };
    const getTrackByName = (target, sendTrack) => {
      if (proxy.props && proxy.props.name && target === proxy.props.name) {
        sendTrack(ctx.attrs.path);
      }
    };
    const getStateByTrack = (path, sendState) => {
      try {
        if (proxy.$.refs.component && proxy.$.refs.component.$attrs.path === path) {
          const component = proxy.$.refs.component;
          const data = component?.state?.data || component.initData;
          sendState(data);
        }
      } catch (e) {
        console.error(e);
      }
    };
    onBeforeMount(() => {
      try {
        if (!proxy.$.appContext.components[`i-${props.renderer}`]) {
          error.value = '';
        }
      } catch (e) {
        console.error(props);
      }
    });
    onMounted(() => {
      if (proxy.props && proxy.props.name) {
        proxy.$eventHub.$on('component:reload', onComponentReload);
        proxy.$eventHub.$on('component:track', getTrackByName);
      }
      if (proxy.$iRenderConfig.editable) {
        proxy.$eventHub.$on('component:state', getStateByTrack);
      }
    });
    onBeforeUnmount(() => {
      if (proxy.props && proxy.props.name) {
        proxy.$eventHub.$off('component:reload', onComponentReload);
        proxy.$eventHub.$off('component:track', getTrackByName);
      }
      if (proxy.$iRenderConfig.editable) {
        proxy.$eventHub.$off('component:state', getStateByTrack);
      }
    });
    onErrorCaptured((err, vm, info) => {
      error.value = `'${err.message}' is found in ${props.renderer} renderer`;
      console.error(err, vm, info);
      return false;
    });

    return {
      path: ctx.attrs.path,
      track: ctx.attrs.track,
      ...useDerivedProps(props),
      filterAction,
      handleFetchApi,
      componentName,
      updatedCount,
      errorInfo,
      state,
    };
  },
});
</script>
