<template>
  <div
    v-if="iVisible"
    v-show="iHidden"
    :class="['i-wrapper__container', `index-${index}`, classname, iComputedClass]"
  >
    <div
      v-if="header"
      class="i-wrapper__container__header"
    >
      <template v-if="$isArray(header)">
        <i-component
          v-for="(child, index) in header"
          :key="`${path}/${index}/${child.renderer}`"
          :renderer="child.renderer"
          :path="`${path}/header/${index}/${child.renderer}`"
          :header="iHeader(child)"
          :body="iBody(child)"
          :footer="iFooter(child)"
          :props="iAttrs(child)"
          :init-data="state.data"
        />
      </template>
      <i-component
        v-else-if="$isObject(header)"
        :renderer="header.renderer"
        :path="`${path}/header/${header.renderer}`"
        :body="iBody(header)"
        :header="iHeader(header)"
        :footer="iFooter(header)"
        :props="iAttrs(header)"
        :init-data="state.data"
      />
      <template v-else-if="$isString(header)">
        {{$renderTpl(header, state.data)}}
      </template>
    </div>
    <div
      v-if="body"
      class="i-wrapper__container__body"
    >
      <template v-if="$isArray(body)">
        <i-component
          v-for="(item, index) in body"
          :key="`${path}/${index}/${item.renderer}`"
          :renderer="item.renderer"
          :path="`${path}/body/${index}/${item.renderer}`"
          :index="index"
          :hidden-on="item.hiddenOn"
          :visible-on="item.visibleOn"
          :header="iHeader(item)"
          :body="iBody(item)"
          :footer="iFooter(item)"
          :props="iAttrs(item)"
          :init-data="state.data"
        />
      </template>
      <i-component
        v-else-if="$isObject(body)"
        :renderer="body.renderer"
        :path="`${path}/body/${body.renderer}`"
        :hidden-on="body.hiddenOn"
        :visible-on="body.visibleOn"
        :body="iBody(body)"
        :header="iHeader(body)"
        :footer="iFooter(body)"
        :props=" iAttrs(body)"
        :init-data="state.data"
      />
      <template v-else-if="$isString(body)">
        {{$renderTpl(body, state.data)}}
      </template>
    </div>
    <div
      v-if="footer"
      class="i-wrapper__container__footer"
    >
      <template v-if="$isArray(footer)">
        <i-component
          v-for="(child, index) in footer"
          :key="`${path}/${index}/${child.renderer}`"
          :renderer="child.renderer"
          :path="`${path}/footer/${index}/${child.renderer}`"
          :header="iHeader(child)"
          :body="iBody(child)"
          :footer="iFooter(child)"
          :props="iAttrs(child)"
          :init-data="state.data"
        />
      </template>
      <i-component
        v-else-if="$isObject(footer)"
        :renderer="footer.renderer"
        :path="`${path}/footer/${footer.renderer}`"
        :header="iHeader(footer)"
        :body="iBody(footer)"
        :footer="iFooter(footer)"
        :props="iAttrs(footer)"
        :init-data="state.data"
      />
      <template v-else-if="$isString(footer)">
        {{$renderTpl(footer, state.data)}}
      </template>
    </div>
  </div>
</template>
<script>
import {defineComponent, computed, getCurrentInstance} from 'vue';
import useDerivedProps from '../../hooks/useDerivedProps';
import useInitData from '../../hooks/useInitData';
import useVisible from '../../hooks/useVisible';
import useLinkage from '../../hooks/useLinkage';
import visible from '../props/visible';
import initData from '../props/init-data';
import mixinProps from '../props/wrapper';

export default defineComponent({
  name: 'Wrapper',
  mixins: [mixinProps, visible, initData],
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const state = useInitData(props);
    const iComputedClass = computed(() => {
      if (props.computedClass) {
        return proxy.$expressionEval(props.computedClass, state.data);
      }
      return '';
    });

    return {
      path: ctx.attrs.path,
      ...useDerivedProps(props),
      ...useVisible(props),
      ...useLinkage(props),
      iComputedClass,
      state,
    };
  },
});
</script>
