<template>
  <section class="i-qa__container">
    <el-divider class="i-qa__title">
      
    </el-divider>
    <ul class="i-qa__list">
      <li
        v-for="(item, index) in iOptions"
        :key="index"
        class="i-qa__list__item"
      >
        <div class="i-qa__list__question">
          <i-icon
            :path="`${path}/${index}/question`"
            class="i-qa__list__icon"
            icon="QuestionFilled"
            size="14"
          />
          {{item.question}}
        </div>
        <div class="i-qa__list__answer">
          {{item.answer}}
        </div>
      </li>
    </ul>
    <el-divider
      v-if="options.length > 1"
      class="i-qa__title"
      @click="showMore"
    >
      {{iOptions.length === 1? '': ''}}
    </el-divider>
  </section>
</template>
<script>
import {defineComponent, ref} from 'vue';
import mixinProps from '../props/qa';

export default defineComponent({
  name: 'Qa',
  mixins: [mixinProps],
  setup(props, ctx) {
    const iOptions = ref([]);
    iOptions.value = [props.options[0]];

    const showMore = () => {
      if (iOptions.value.length === props.options.length) {
        iOptions.value = [props.options[0]];
      } else {
        iOptions.value = props.options;
      }
    };

    return {
      path: ctx.attrs.path,
      iOptions,
      showMore
    };
  }
});
</script>
