<template>
  <el-sub-menu
    :index="index + ''"
    :popper-class="popperClass"
    :class="classname"
    :show-timeout="showTimeout"
    :hide-timeout="hideTimeout"
    :disabled="disabled"
    :popper-append-to-body="appendToBody"
  >
    <template
      v-if="title"
      #title
    >
      <i-icon :path="`${path}/submenu`" :icon="icon" />
      <span>{{ title }}</span>
    </template>
    <template
      v-for="(item, jndex) in body"
      :key="jndex"
    >
      <i-component
        :path="`${path}/${jndex}/${item.renderer}`"
        :renderer="item.renderer"
        :body="item.body"
        :props="item"
        :index="index + '-' + jndex"
      />
    </template>
  </el-sub-menu>
</template>

<script>
import { defineComponent } from 'vue';
import useDerivedProps from '../../../hooks/useDerivedProps';

import mixinProps from '../../props/menu/menu-submenu';

export default defineComponent({
  name: 'Submenu',
  mixins: [mixinProps],
  setup(props, ctx) {
    return {
      path: ctx.attrs.path,
      ...useDerivedProps(props),
    };
  },
});
</script>
