import {onBeforeMount, reactive, watch, getCurrentInstance} from 'vue';
import {getUrlParams} from '../utils/url';

export default function (props) {
  const { proxy } = getCurrentInstance();
  const pageInfo = reactive({
    pageIndex: 1,
    pageSize: 10,
    total: 0,
    hasMore: true,
    layout: 'prev, pager, next, jumper, sizes, total'
  });
  watch(() => pageInfo.pageIndex, () => {
    proxy.fetchDataList();
  });
  watch(() => pageInfo.pageSize, () => {
    pageInfo.pageIndex = 1;
    proxy.fetchDataList();
  });
  onBeforeMount(() => {
    if (props.pageConfig.layout) {
      pageInfo.layout = props.pageConfig.layout;
    }
    if (props.hasPageInfo) {
      const defaultQuery = getUrlParams();
      const { pageIndex, pageSize } = defaultQuery;
      if (pageIndex) {
        pageInfo.pageIndex = Number(pageIndex);
      }
      if (pageSize) {
        pageInfo.pageSize = Number(pageSize);
      }
    }
  });

  return pageInfo;
}
