<template>
  <div class="i-editor__container">
    <transition name="el-zoom-in-center">
      <el-alert
        v-if="state.showErrorBoundary"
        class="i-editor__container__error-info"
        :title="state.errorInfo"
        type="error"
        show-icon
        @close="closeErrorInfo"
      />
    </transition>
    <template v-if="state.iJson">
      <div
        v-loading="state.loadingMonaco"
        ref="editor"
        class="monaco-editor"
      />
    </template>
    <toolmaker
      v-if="!state.iJson"
      ref="toolmaker"
      :path="`${path}/toolmaker`"
    />
    <transition name="el-zoom-in-bottom">
      <div
        v-if="(!state.showErrorBoundary && editable)"
        class="i-editor__container__tools"
      >
        <el-button
          v-if="state.iJson"
          size="small"
          type="primary"
          @click="onChange"
        >
          
        </el-button>
        <el-button
          v-if="!state.iJson"
          size="small"
          type="primary"
          @click="onChange"
        >
          
        </el-button>
        <el-button
          size="small"
          type="primary"
          @click="onSave"
        >
          
        </el-button>
        <el-button
          size="small"
          type="danger"
          @click="onSaveRemote"
        >
          
        </el-button>
      </div>
    </transition>
  </div>
</template>
<script>
import {getCurrentInstance, defineComponent, defineAsyncComponent, reactive, ref, watch, onBeforeMount, onMounted, onBeforeUnmount, nextTick} from 'vue';
import initApi from '../../renderer/props/init-api';

const DEFAULT_CONFIG = {
  theme: 'vs',
  inherit: false,
  fontSize: '14px',
  autoIndent: true,
  formatOnType: true,
  formatOnPaste: true,
  selectOnLineNumbers: true,
  scrollBeyondLastLine: false,
  folding: true,
  automaticLayout: true,
  minimap: {
    enabled: false,
  },
};
export default defineComponent({
  name: 'i-editor',
  components: {
    Toolmaker: defineAsyncComponent(() => import('./toolmaker.vue')),
  },
  mixins: [initApi],
  props: {
    path: {
      type: String,
      required: true,
    },
    schema: {
      type: Object,
      required: false,
      default: function() {
        return {};
      },
    },
    editable: {
      type: Boolean,
      required: false,
      default: false,
    },
    isJson: {
      type: Boolean,
      required: false,
      default: true,
    },
  },
  setup(props) {
    const { proxy } = getCurrentInstance();
    const state = reactive({
      errorInfo: '',
      iJson: false,
      iSchema: {},
      pageInfo: {},
      loadingMonaco: false,
      showErrorBoundary: false,
    });
    const iSchema = ref('');
    let editor;
    const initEditor = async() => {
      try {
        const MonacoEditor = await import(/* webpackChunkName: "monaco-editor", webpackPrefetch: true */ 'monaco-editor/esm/vs/editor/editor.api');
        if (props.editable) {
          const { pageSchema } = window.IRender;
          editor = MonacoEditor.editor.create(proxy.$.refs.editor, {
            ...DEFAULT_CONFIG,
            language: 'json',
            theme: 'vs-dark',
          });
          updateSchema(pageSchema);
        } else {
          editor = MonacoEditor.editor.create(proxy.$.refs.editor, {
            ...DEFAULT_CONFIG,
            theme: 'vs',
            language: 'json',
          });
          updateSchema();
        }
        state.loadingMonaco = false;
      } catch (e) {
        proxy.$message.error(e.message);
        console.error(e);
      }
    };
    const handleFormatSchema = () => {
      const timer = setTimeout(() => {
        editor
            .getAction(['editor.action.formatDocument'])
            .run()
            .then(() => {
              clearTimeout(timer);
            })
      }, 200);
    };
    const onChange = () => {
      state.iJson = !state.iJson;
      if (state.iJson) {
        nextTick(() => {
          state.loadingMonaco = true;
          initEditor();
        });
      }
    };
    const updateSchema = (pageSchema) => {
      if (pageSchema) {
        iSchema.value = pageSchema;
      }
      editor.setValue(JSON.stringify(iSchema.value));
      handleFormatSchema();
    };
    const onSaveRemote = () => {
      onSave().then((pageSchema) => {
        proxy.$api
            .slientApi()
            .put('/api/page', {
              pageSchema: JSON.stringify(pageSchema),
              ...state.pageInfo,
            })
            .then((res) => {})
            .catch((error) => {
              proxy.$message({
                message: error.message,
                showClose: true,
                type: ' error',
              });
            });
      });
    };
    const onSave = () => new Promise((resolve, reject) => {
      try {
        let schema;
        if (state.iJson) {
          const json = editor.getValue();
          schema = JSON.parse(json);
          handleFormatSchema(editor);
        } else {
          schema = proxy.$.refs.toolmaker.schemaInstance;
        }
        proxy.$eventHub.$emit('schema:change', schema);
        proxy.$message({
          message: '',
          showClose: true,
          type: 'success',
        });
        resolve(schema);
      } catch (e) {
        state.errorInfo = e;
        state.showErrorBoundary = true;
        state.errorInfoTimer = setTimeout(() => {
          closeErrorInfo();
          clearTimeout(state.errorInfoTimer);
        }, 3500);
        reject(e);
      }
    });
    const closeErrorInfo = () => {
      state.showErrorBoundary = false;
    };
    watch(() => props.schema, (val) => {
      iSchema.value = val;
    }, {
      immediate: true,
    });
    watch(() => props.isJson, val => {
      state.iJson = val;
    }, {
      immediate: true
    });
    onBeforeMount(() => {
      proxy.$iRenderConfig.editable = true;
    });
    onMounted(() => {
      const { pageInfo } = window.IRender;
      state.pageInfo = pageInfo;
    });
    onBeforeUnmount(() => {
      proxy.$iRenderConfig.editable = false;
      proxy.$eventHub.$emit('portal:hidden');
    });
    return {
      initEditor,
      onChange,
      updateSchema,
      onSave,
      onSaveRemote,
      closeErrorInfo,
      state
    };
  },
});
</script>
