<template>
  <el-upload
    ref="upload"
    with-credentials
    :action="actionUrl"
    :on-exceed="handleExceed"
    :on-success="handleSuccess"
    :on-error="handleError"
    :file-list="fileList"
    :limit="limit"
    :headers="headers"
    :auto-upload="false"
    :data="params"
  >
    <el-button
      size="small"
      type="primary"
    >
      
    </el-button>
    <el-button
      size="small"
      type="success"
      @click="submitUpload"
    >
      
    </el-button>
    <template
      v-if="tip"
      #tip
    >
      <div class="el-upload__tip">
        {{ tip }}
      </div>
    </template>
  </el-upload>
</template>

<script>
import {defineComponent, getCurrentInstance, onMounted, reactive, ref} from 'vue';
import useInitData from '../../../hooks/useInitData';
import mixinProps from '../../props/form/upload';
import initData from '../../props/init-data';

export default defineComponent({
  name: 'Upload',
  mixins: [mixinProps, initData],
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const state = useInitData(props);
    const headers = reactive({});
    const fileList = reactive([]);

    const submitUpload = () => {
      event.stopPropagation();
      event.preventDefault();

      proxy.$refs.upload.submit();
    };
    const handleExceed = (files, fileList) => {
      proxy.$message.warning(`${props.limit} ${files.length}  ${files.length + fileList.length} `);
    };
    const handleSuccess = (res) => {
      if (res.data) {
        const feedbackData = {};
        for (let key in props.feedbackConfig) {
          if (props.feedbackConfig.hasOwnProperty(key)) {
            feedbackData[key] = res.data[key];
          }
        }
        props.action && props.action(feedbackData);
      } else {
        proxy.$confirm('');
      }
    };
    const handleError = (err) => {
      if (err && err.msg) {
        proxy.$confirm(err.msg);
      } else {
        proxy.$confirm('');
      }
    };

    onMounted(() => {
      const cmsToken = localStorage.getItem('cmsToken');
      headers.value = {
        Authorization: cmsToken
      };
    });
    return {
      path: ctx.attrs.path,
      submitUpload,
      handleExceed,
      handleSuccess,
      handleError,
      fileList,
      headers,
      state
    };
  },
});
</script>
