<template>
  <div
    v-loading="loading"
    class="i-chart__container"
    :style="{height: `${defaultHeight}px`}"
  >
    <div
      v-if="!error"
      ref="chart"
      :style="{height: `${defaultHeight}px`}"
    />
    <el-result
      v-else
      icon="error"
      title=""
      class="i-chart__container__error"
    >
      <template #extra>
        <el-button
          type="primary"
          size="small"
          @click="onComponentUpdate"
        >
          
        </el-button>
      </template>
    </el-result>
  </div>
</template>
<script>
import {defineComponent, getCurrentInstance, onMounted, ref, watch} from 'vue';
import {cloneDeep} from 'lodash';
import {appendScript} from '../../utils/appendAssets';
import {config} from '../../utils/config';

import useInitApi from '../../hooks/useInitApi';
import useInitData from '../../hooks/useInitData';
import useDerivedProps from '../../hooks/useDerivedProps';
import initApi from '../props/init-api';
import initData from '../props/init-data';
import mixinProps from '../props/chart';

export default defineComponent({
  name: 'Chart',
  mixins: [mixinProps, initApi, initData],
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const state = useInitData(props);
    const { handleFetchApi, shouldComponentUpdate } = useInitApi(props);
    const loading = ref(false);
    const error = ref(false);
    const defaultHeight = ref(200);
    const iConfig = ref();
    iConfig.value = cloneDeep(props.config);
    const handleClick = (data) => {
      if (!props.actions) {
        return;
      }
      const {
        $vars, encode, event, dimensionNames, ...other
      } = data;
      props.action(props.actions, other);
    };
    const compiledConfig = (config = {}) => {
      const temp = JSON.stringify(config);
      const option = temp.replace(/"\${([^}{]+?)}"/g, (escape, name) => {
        return JSON.stringify(state.data[name]) || '""';
      });
      return JSON.parse(option);
    };
    const onComponentUpdate = () => {
      error.value = false;
      loading.value = true;
      if (props.initApi) {
        fetchDataList();
      } else if (props.config) {
        const defaultConfig = cloneDeep(props.config);
        iConfig.value = compiledConfig(defaultConfig);
        initChart();
      }
    };
    const fetchDataList = () => {
      const apiData = props.initApi;
      handleFetchApi(apiData, state.data)
        .then((result) => {
          if (result) {
            if (!props.config) {
              iConfig.value = result;
            } else {
              state.data = { ...state.data, ...result };
              const defaultConfig = cloneDeep(props.config);
              iConfig.value = compiledConfig(defaultConfig);
            }
            initChart();
          }
        })
        .catch(err => {
          error.value = true;
          console.error(err);
        })
        .finally(() => {
          loading.value = false;
        });
    };
    const initChart = () => {
      const chinaMap = {}; //await import(/* webpackChunkName: "chinaMap", webpackPrefetch: true */ '../../utils/chinaMap.json');
      appendScript('echarts')
        .then(() => {
          const chartFactory = window.echarts.init(proxy.$refs.chart);
          // res.registerMap('china', chinaMap);
          chartFactory.setOption(iConfig.value);
          chartFactory.on('click', handleClick);
        }).catch(e => {
          proxy.$message({
            message: e.message,
            showClose: true,
            grouping: true,
            type: 'error',
          });
          error.value = true;
          console.error(e);
        }).finally(() => {
          loading.value = false;
        });
    };

    watch(() => state.data, () => {
      onComponentUpdate();
    }, {
      deep: true
    });
    onMounted(() => {
      defaultHeight.value = props.height || proxy.$refs.chart?.$el?.parentNode?.offsetWidth * 0.618;
      onComponentUpdate();
    });

    return {
      path: ctx.attrs.path,
      ...useDerivedProps(props),
      shouldComponentUpdate,
      onComponentUpdate,
      handleClick,
      defaultHeight,
      loading,
      error,
      state,
    };
  },
});
</script>
