<template>
  <el-card
    v-if="iVisible"
    v-show="iHidden"
    :shadow="shadow"
    :class="['i-card', classname]"
  >
    <template
      v-if="header && header.length"
      #header
    >
      <template v-if="$isArray(header)">
        <template
          v-for="(child, index) in header"
          :key="`${path}/${index}/${child.renderer}`"
        >
          <i-component
            :renderer="child.renderer"
            :path="`${path}/header/${index}/${child.renderer}`"
            :props="iAttrs(child)"
            :header="iHeader(child)"
            :body="iBody(child)"
            :footer="iFooter(child)"
            :init-data="state.data"
          />
        </template>
      </template>
      <i-component
        v-else-if="$isObject(header)"
        :renderer="header.renderer"
        :path="`${path}/header/${header.renderer}`"
        :body="iBody(header)"
        :header="iHeader(header)"
        :footer="iFooter(header)"
        :props="iAttrs(header)"
        :init-data="state.data"
      />
      <template v-else-if="$isString(header)">
        {{$renderTpl(header, state.data)}}
      </template>
    </template>
    <div
      v-if="body"
      class="i-card__body"
    >
      <template v-if="$isArray(body)">
        <template
          v-for="(child, index) in body"
          :key="`${path}/${index}/${child.renderer}`"
        >
          <i-component
            :renderer="child.renderer"
            :path="`${path}/body/${index}/${child.renderer}`"
            :props="iAttrs(child)"
            :header="iHeader(child)"
            :body="iBody(child)"
            :footer="iFooter(child)"
            :init-data="state.data"
          />
        </template>
      </template>
      <i-component
        v-else-if="$isObject(body)"
        :renderer="body.renderer"
        :path="`${path}/body/${body.renderer}`"
        :props="iAttrs(body)"
        :header="iHeader(body)"
        :body="iBody(body)"
        :footer="iFooter(body)"
        :init-data="state.data"
      />
      <template v-else-if="$isString(body)">
        {{$renderTpl(body, state.data)}}
      </template>
    </div>
    <div
      v-if="footer"
      class="i-card__footer"
    >
      <template v-if="$isArray(footer)">
        <template
          v-for="(child, index) in footer"
          :key="`${path}/${index}/${child.renderer}`"
        >
          <i-component
            :renderer="child.renderer"
            :path="`${path}/footer/${index}/${child.renderer}`"
            :header="iHeader(child)"
            :body="iBody(child)"
            :footer="iFooter(child)"
            :props="iAttrs(child)"
            :init-data="state.data"
          />
        </template>
      </template>
      <i-component
        v-else-if="$isObject(footer)"
        :renderer="footer.renderer"
        :path="`${path}/footer/${footer.renderer}`"
        :header="iHeader(footer)"
        :body="iBody(footer)"
        :footer="iFooter(footer)"
        :props="iAttrs(footer)"
        :init-data="state.data"
      />
      <template v-else-if="$isString(footer)">
        {{$renderTpl(footer, state.data)}}
      </template>
    </div>
  </el-card>
</template>

<script>
import { defineComponent, getCurrentInstance } from 'vue';

import useDerivedProps from '../../hooks/useDerivedProps';
import useInitData from '../../hooks/useInitData';
import useLinkage from '../../hooks/useLinkage';
import useVisible from '../../hooks/useVisible';
import visible from '../props/visible';
import initData from '../props/init-data';
import mixinProps from '../props/card';

export default defineComponent({
  name: 'Card',
  mixins: [mixinProps, visible, initData],
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const state = useInitData(props);
    const renderHeader = () => proxy.$getRenderedTpl(props.header, state.data);

    return {
      path: ctx.attrs.path,
      ...useDerivedProps(props),
      ...useLinkage(props),
      ...useVisible(props),
      renderHeader,
      state,
    };
  },
});
</script>
