<template>
  <el-select
    v-model="iModelValue[name]"
    :name="name"
    :size="size"
    :disabled="disabled"
    :clearable="clearable"
    :multiple="multiple"
    :filterable="filterable"
    :placeholder="placeholder"
    :popper-class="`i-select-popper ${popperclass}`"
  >
    <el-option
      v-for="option in state.data.options"
      :key="option.value"
      :value="option.value"
      :label="option.text"
      :disabled="option.disabled"
    >
      <template v-if="$isArray(body)">
        <template
          v-for="(child, index) in option.body"
          :key="`${path}/${index}/${child.renderer}`"
        >
          <i-component
            v-bind="iAttrs(child)"
            :renderer="child.renderer"
            :path="`${path}/body/${index}/${child.renderer}`"
            :props="iAttrs(child)"
            :header="iHeader(child)"
            :body="iBody(child)"
            :footer="iFooter(child)"
          />
        </template>
      </template>
      <i-component
        v-else-if="$isObject(option.body)"
        v-bind="iAttrs(option.body)"
        :renderer="option.body.renderer"
        :path="`${path}/body/${option.body.renderer}`"
        :header="iHeader(option.body)"
        :body="iBody(option.body)"
        :footer="iFooter(option.body)"
        :props="iAttrs(option.body)"
      />
    </el-option>
  </el-select>
</template>
<script>
import {getCurrentInstance, defineComponent, watch, reactive, onMounted} from 'vue';
import useDerivedProps from '../../../hooks/useDerivedProps';
import useInitApi from '../../../hooks/useInitApi';
import useInitData from '../../../hooks/useInitData';
import initData from '../../props/init-data';
import initApi from '../../props/init-api';
import mixinProps from '../../props/form/select';

export default defineComponent({
  name: 'Select',
  mixins: [mixinProps, initApi, initData],
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const { handleFetchApi } = useInitApi(props);
    const state = useInitData(props);
    const iModelValue = reactive({});
    const optionsAdaptor = result => {
      if (proxy.$isArray(result)) {
        state.data.options = result.map(item => {
          item.text = item[props.optionConfig.label];
          item.value = item[props.optionConfig.value];
          return item;
        });
      } else if (proxy.$isObject(result)) {
        state.data.options = Object.keys(result).map(item => {
          return {
            text: result[item],
            value: item
          };
        });
      }
    };
    const fetchDataList = () => {
      const apiData = props.initApi;
      handleFetchApi(apiData, state.data)
        .then((result) => optionsAdaptor(result));
    };

    watch(() => props.options, (val) => {
      if (typeof val === 'string') {
        const result = state.data[val];
        optionsAdaptor(result);
      } else if (proxy.$isArray(val)) {
        state.data.options = val;
      }
    }, {
      immediate: true,
    });

    watch(() => props.defaultValue, val => {
      iModelValue[props.name] = val;
    }, {
      immediate: true,
    });

    watch(() => props.modelValue, val => {
      iModelValue[props.name] = val || '';
    }, {
      immediate: true,
    });

    watch(() => iModelValue[props.name], val => {
      ctx.emit('update:modelValue', val);
      if (props.target) {
        props.linkageTrigger(props.target, iModelValue);
      }
    });

    onMounted(() => {
      if (props.initApi) {
        fetchDataList();
      }
    });

    return {
      ...useDerivedProps(),
      path: ctx.attrs.path,
      iModelValue,
      state,
    };
  },
});
</script>
