<template>
  <div ref="richtxt" class="i-form__richtext" />
</template>
<script>
import {getCurrentInstance, defineComponent, reactive, watch, onMounted, onBeforeUnmount} from 'vue';
import {debounce} from 'lodash';
import {appendScript} from '../../../utils/appendAssets';
import useInitData from '../../../hooks/useInitData';
import useVisible from '../../../hooks/useVisible';
import visible from '../../props/visible';
import initData from '../../props/init-data';
import mixinProps from '../../props/form/richtxt';

export default defineComponent({
  name: 'Richtxt',
  mixins: [mixinProps, visible, initData],
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const state = useInitData(props);
    const iModelValue = reactive({});
    let richEditor;
    const onRichtxtChange = debounce(txt => {
      iModelValue[props.name] = txt;
    }, 500);
    watch(() => props.defaultValue, (val) => {
      iModelValue[props.name] = val;
    }, {
      immediate: true,
    });

    watch(() => props.modelValue, (val) => {
      iModelValue[props.name] = val || 0;
    }, {
      immediate: true,
    });

    watch(() => iModelValue[props.name], (val) => {
      ctx.emit('update:modelValue', val);
    });

    onMounted(() => {
      appendScript('richeditor')
        .then(() => {
          richEditor = new window.wangEditor(proxy.$refs.richtxt);
          richEditor.customConfig.zIndex = 1;
          richEditor.customConfig.onchange = onRichtxtChange;
          richEditor.create();
          richEditor.txt.html(iModelValue[props.name] || '');
        }).catch(e => {
          proxy.$message({
            message: e.message,
            showClose: true,
            grouping: true,
            type: 'error',
          });
          console.error(e);
        });
    });
    onBeforeUnmount(() => {
      richEditor && richEditor.destroy && richEditor.destroy();
    });
    return {
      path: ctx.attrs.path,
      ...useVisible(props),
      iModelValue,
      state,
    };
  }
});
</script>
