<template>
  <el-icon
    v-if="type === 'icon'"
    :class="['i-icon__container', {invert}, classname]"
    :icon="icon"
    :style="{
      width: `${size}px`,
      height: `${size}px`,
      fontSize: `${size}px`,
      verticalAlign: 'middle',
      '--color': color,
      '--bgColor': bgColor,
      borderRadius: `${borderRadius}px`,
    }"
  >
    <component :is="icon" />
  </el-icon>
  <font-awesome-icon
    v-else
    :class="['i-icon__container', {invert}, classname]"
    :icon="icon"
    :style="{
      width: `${size}px`,
      height: `${size}px`,
      fontSize: `${size}px`,
      verticalAlign: 'middle',
      '--color': color,
      '--bgColor': bgColor,
      borderRadius: `${borderRadius}px`,
    }"
  />
</template>

<script>
import {defineComponent} from 'vue';
import useInitData from '../../hooks/useInitData';
import initData from '../props/init-data';
import mixinProps from '../props/icon';


export default defineComponent({
  name: 'Icon',
  mixins: [mixinProps, initData],
  setup(props, ctx) {
    const state = useInitData(props);
    const handleClick = () => {
      props.actionType
        && props.action({
          actionType: props.actionType,
          ...ctx.attrs,
        }, props.initData);
    };

    return {
      handleClick,
      state
    };
  },
});
</script>
