<template>
  <el-switch
    v-if="iVisible"
    v-show="iHidden"
    v-model="iModelValue[name]"
    :disabled="disabled"
    :width="width"
    :class="classname"
    :active-icon-class="activeIconClass"
    :active-text="activeText"
    :inactive-text="inActiveText"
    :active-value="activeValue"
    :inactive-value="inActiveValue"
    :active-color="activeColor"
    :inactive-color="inActiveColor"
  />
</template>
<script>
import {defineComponent, onBeforeMount, onMounted, onBeforeUnmount, getCurrentInstance, reactive, watch} from 'vue';
import useLinkage from '../../../hooks/useLinkage';
import useInitData from '../../../hooks/useInitData';
import useVisible from '../../../hooks/useVisible';
import visible from '../../props/visible';
import initData from '../../props/init-data';
import mixinProps from '../../props/form/switch';

export default defineComponent({
  name: 'Switch',
  mixins: [mixinProps, visible, initData],
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const { onLinkageTrigger } = useLinkage(props);
    const state = useInitData(props);
    const iModelValue = reactive({
      [props.name]: props.modelValue || props.initData[props.name] || props.defaultValue || false
    });
    const handleRemoteClick = (actionType, target, onActionFeedback) => {
      if (target && target === props.name) {
        iModelValue[props.name] = !iModelValue[props.name];
        ctx.emit('update:modelValue', iModelValue[props.name]);
        onLinkageTrigger(props.target, iModelValue);
        onActionFeedback&&onActionFeedback('CANCEL_LOADING');
      }
    };
    watch(() => props.defaultValue, (val) => {
      iModelValue[props.name] = val || false;
    });
    watch(() => props.modelValue, (val) => {
      iModelValue[props.name] = val || false;
    });
    watch(() => iModelValue[props.name], (val) => {
      ctx.emit('update:modelValue', val);
      if (props.target) {
        onLinkageTrigger(props.target, iModelValue);
      }
    });
    onMounted(() => {
      proxy.$eventHub.$on('component:remoteComponent', handleRemoteClick);
    });
    onBeforeUnmount(() => {
      proxy.$eventHub.$off('component:remoteComponent', handleRemoteClick);
    });
    return {
      path: ctx.attrs.path,
      ...useVisible(props),
      state,
      iModelValue,
    };
  },
});
</script>
