<template>
  <el-form
    v-loading="loading&&!silentLoading"
    :label-width="labelWidth"
    :label-position="labelPosition"
    :model="state.data"
    :inline="inline"
    ref="form"
    class="i-form__container"
  >
    <template v-if="!fieldset">
      <template
        v-for="(item, index) in controls"
        :key="`${path}/controls/${index}/${item.renderer}`"
      >
        <i-field
          v-if="$formItems.includes(item.renderer)"
          v-model="state.data[item.name]"
          :path="`${path}/controls/${index}`"
          :track="`${path}/controls/${index}`"
          :name="item.name"
          :read-only="readOnly"
          :props="{...item, loading}"
          :init-data="state.data"
          :hidden-on="item.hiddenOn"
          :visible-on="item.visibleOn"
          :disabled-on="item.disabledOn"
          :field-blur="onFieldBlur"
          :handle-invisible="handleInvisible"
          :linkageTrigger="onLinkageTrigger"
          :action="formActionMap(item)"
        />
        <el-form-item
          v-else
          :class="{
            'i-form__actions__wrapper': true,
            'i-form__actions--inline': item.renderer === 'action',
          }"
        >
          <i-component
            :ref="item.actionType"
            :path="`${path}/controls/${index}`"
            :track="`${path}/controls/${index}`"
            :renderer="item.renderer"
            :header="iHeader(item)"
            :body="iBody(item)"
            :footer="iFooter(item)"
            :props="iAttrs(item)"
            :hidden-on="item.hiddenOn"
            :visible-on="item.visibleOn"
            :disabled-on="item.disabledOn"
            :init-data="state.data"
            :action="formActionMap(item)"
          />
        </el-form-item>
      </template>
    </template>
    <template v-else>
      <el-collapse v-model="activeCollapse">
        <fieldset
          v-for="(control, index) in controls"
          :key="`${path}/${index}`"
          :class="control.classname"
          class="i-form__container__fieldset"
        >
          <el-collapse-item :name="index">
            <template #title>
              <legend>
                <span class="i-form__container__legend">
                  {{ control.title }}
                </span>
              </legend>
            </template>
            <div class="i-form__container__fieldset__items">
              <template
                v-for="(item, jndex) in control.controls"
                :key="`${path}/${jndex}/${item.renderer}`"
              >
                <i-field
                  v-if="$formItems.includes(item.renderer)"
                  v-model="state.data[item.name]"
                  :path="`${path}/${jndex}`"
                  :name="item.name"
                  :read-only="readOnly"
                  :props="{...item, loading}"
                  :init-data="state.data"
                  :hidden-on="item.hiddenOn"
                  :visible-on="item.visibleOn"
                  :disabled-on="item.disabledOn"
                  :handle-invisible="handleInvisible"
                  :formitems="$formItems"
                  :action="formActionMap(item)"
                />
                <i-component
                  v-else
                  :path="`${path}/${item.renderer}`"
                  :renderer="item.renderer"
                  :header="iHeader(item)"
                  :body="iBody(item)"
                  :footer="iFooter(item)"
                  :props="iAttrs(item)"
                  :init-data="state.data"
                  :action="formActionMap(item)"
                />
              </template>
            </div>
            <div
              v-if="control.viewer"
              class="i-form__container__viewer"
            >
              <i-component
                :path="`${path}/${control.viewer.renderer}`"
                :renderer="control.viewer.renderer"
                :header="iHeader(control.viewer)"
                :body="iBody(control.viewer)"
                :footer="iFooter(control.viewer)"
                :props="iAttrs(control.viewer)"
                :init-data="state.data"
              />
            </div>
          </el-collapse-item>
        </fieldset>
      </el-collapse>
    </template>
  </el-form>
</template>
<script>
import {cloneDeep} from 'lodash';
import {defineComponent, onMounted, getCurrentInstance, onBeforeUnmount, nextTick, ref} from 'vue';
import useInitApi from '../../../hooks/useInitApi';
import useDerivedProps from '../../../hooks/useDerivedProps';
import useInitData from '../../../hooks/useInitData';
import useLinkage from '../../../hooks/useLinkage';
import initApi from '../../props/init-api';
import initData from '../../props/init-data';
import mixinProps from '../../props/form/form';
import {createValid, clearValid} from '../../../utils/valid';

export default defineComponent({
  name: 'Form',
  mixins: [mixinProps, initApi, initData],
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const { handleFetchApi } = useInitApi(props);
    const { onLinkageTrigger } = useLinkage(props);
    const state = useInitData(props);
    const activeCollapse = [];
    const data = {};
    const loading = ref(false);
    for (let i = 0; i < props.controls.length; i++) {
      let controls;
      if (props.fieldset && props.controls[i].renderer !== 'combo') {
        controls = props.controls[i].controls;
      } else {
        controls = [props.controls[i]];
      }
      activeCollapse.push(i);
      controls.forEach((control) => {
        const { renderer } = control;
        const { name } = control;
        let value = state.data[name] || control.defaultValue;
        if (renderer === 'calendar' && !value) {
          value = new Date();
        }
        if (name && renderer !== 'action') {
          if (typeof value === 'undefined') {
            value = '';
          }
          data[name] = value;
        }
        if (control.rules) {
          createValid(control, state.data, proxy, name);
        }
      });
    }
    Object.assign(state.data, data);
    let invisibleField = [];
    const onComponentUpdate = () => {
      if (props.initApi && props.initApi.url) {
        fetchDataList();
      }
    };
    const fetchDataList = () => {
      const apiData = cloneDeep(props.initApi);
      loading.value = true;
      handleFetchApi(apiData, state.data)
        .then((result) => {
          Object.assign(state.data, result);
        })
        .catch((err) => {
          console.error(err);
        })
        .finally(() => {
          loading.value = false;
        });
    };
    const handleInvisible = (visible, field) => {
      if (visible) {
        invisibleField = invisibleField.filter(
          (item) => item !== field,
        );
      } else {
        loading.value = true;
        invisibleField.push(field);
        handleResetField(field);
      }
    };
    const handleResetField = (field) => {
      const type = Object.prototype.toString.call(state.data[field]);
      switch (type) {
        case '[object String]': state.data[field] = ''; break;
        case '[object Number]': state.data[field] = 0; break;
        case '[object Boolean]': state.data[field] = false; break;
        case '[object Array]': state.data[field] = []; break;
        case '[object Object]': state.data[field] = {}; break;
        default: state.data[field] = ''; break;
      }
      nextTick(() => {
        loading.value = false;
      });
    };
    const handleRemoteSubmit = (actionType, target, onActionFeedback) => {
      if (props.name && props.name === target) {
        const { form } = proxy.$.refs;
        if (form && actionType === 'reset') {
          form.resetFields();
        } else if (form && actionType === 'submit') {
          form.validate((valid, fields) => {
            if (valid) {
              sendFormData(onActionFeedback);
            } else {
              form.scrollToField(Object.keys(fields)[0]);
              onActionFeedback&&onActionFeedback('CANCEL_LOADING');
            }
          });
        }
      }
    };
    const handleItemFeedback = (data) => {
      Object.assign(state.data, data);
    };
    const formActionMap = (item) => {
      if (item.renderer === 'upload') {
        return handleItemFeedback;
      }
      if (item.actionType === 'submit') {
        return onBeforeSubmit;
      }
      if (item.actionType === 'reset') {
        return resetForm;
      }
    };
    const resetForm = (onActionFeedback) => {
      const { form } = proxy.$.refs;
      form.resetFields();
      onActionFeedback&&onActionFeedback('CANCEL_LOADING');
    };
    const onBeforeSubmit = (onActionFeedback) => {
      const { form } = proxy.$.refs;
      form.validate((valid, fields) => {
        if (valid) {
          sendFormData();
        } else {
          form.scrollToField(Object.keys(fields)[0]);
        }
      });
      onActionFeedback&&onActionFeedback('CANCEL_LOADING');
    };
    const sendFormData = (onActionFeedback) => {
      const formData = {};
      for (const name in state.data) {
        if (
          Object.hasOwnProperty.call(state.data, name)
          && !invisibleField.includes(name)
        ) {
          formData[name] = state.data[name];
        }
      }
      if (props.target) {
        formData.timestamp = new Date().getTime();
        return onLinkageTrigger(props.target, formData);
      }
      loading.value = true;
      handleFetchApi(
        {
          url: props.actionApi.url || '',
          method: props.actionApi.method || 'post',
          params: formData,
        },
        formData,
      ).then((res) => {
        Object.assign(state.data, res);
        onActionFeedback&&onActionFeedback(res);
        onAfterSubmit(res);
      })
      .catch(err => {
        console.error(err);
        onActionFeedback&&onActionFeedback('CANCEL_LOADING');
      })
      .finally(() => {
        loading.value = false;
      });
    };
    const onAfterSubmit = (res) => {
      if (props.redirect) {
        proxy.$parent.filterAction({actionType: 'url', url: props.redirect}, state.data);
      } else if (props.target && props.linkageType === 'after') {
        return onLinkageTrigger(props.target, res);
      } else if (props.reload) {
        proxy.$eventHub.$off('component:remoteComponent', handleRemoteSubmit);
        proxy.$eventHub.$emit('component:reload', props.reload, state.data);
      }
    };
    const onFieldBlur = () => {
      const { form } = proxy.$.refs;
      form.validate((valid) => {
        if (valid) {
          onComponentUpdate();
        }
      });
    };

    onMounted(() => {
      onComponentUpdate();
      proxy.$eventHub.$on('component:remoteComponent', handleRemoteSubmit);
    });
    onBeforeUnmount(() => {
      clearValid();
      proxy.$eventHub.$off('component:remoteComponent', handleRemoteSubmit);
    });

    return {
      path: ctx.attrs.path,
      ...useDerivedProps(props),
      ...useLinkage(props),
      onComponentUpdate,
      handleInvisible,
      onBeforeSubmit,
      handleItemFeedback,
      handleFetchApi,
      formActionMap,
      onLinkageTrigger,
      onFieldBlur,
      activeCollapse,
      invisibleField,
      loading,
      state,
    };
  },
});
</script>
