<template>
  <div :class="['i-form__list', classname]">
    <component
      v-model="iModelValue[name]"
      :is="multiple? 'el-checkbox-group': 'el-radio-group'"
      :min="min"
      :max="max"
    >
      <template
        v-for="(item, index) in iOptions"
        :key="index"
      >
        <div
          class="i-list__item"
          :class="{ checked: iModelValue[name].includes(item.value) }"
          @click="handleClick(index)"
        >
          <component
            :is="multiple? 'el-checkbox': 'el-radio'"
            :ref="setRef"
            :label="item.value"
          />
          <i-component
            :renderer="body.renderer"
            :path="`${path}/${body.renderer}`"
            :index="index"
            :props="iAttrs(body)"
            :header="iHeader(body)"
            :body="iBody(body)"
            :footer="iFooter(body)"
            :init-data="item"
          />
        </div>
      </template>
    </component>
  </div>
</template>
<script>
import {getCurrentInstance, defineComponent, reactive, watch} from 'vue';

import useDerivedProps from '../../../hooks/useDerivedProps';
import useInitData from '../../../hooks/useInitData';
import initData from '../../props/init-data';
import mixinProps from '../../props/form/list';

export default defineComponent({
  name: 'List',
  mixins: [mixinProps, initData],
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const state = useInitData(props);
    const iModelValue = reactive({});
    const iOptions = reactive(props.options);
    const setRef = (el) => optionRefs.push(el);
    const handleClick = (index) => {
      optionRefs[index].$el.click();
    };
    const optionRefs = [];

    watch(() => props.defaultValue, (val) => {
      let defaultValue = val;
      if (props.multiple && proxy.$isArray(val)) {
        if (val) {
          defaultValue = [val];
        } else {
          defaultValue = [];
        }
      }
      iModelValue[props.name] = defaultValue;
    }, {
      immediate: true,
    });

    watch(() => props.modelValue, (val) => {
      let defaultValue = val;
      if (props.multiple && proxy.$isArray(val)) {
        if (val) {
          defaultValue = [val];
        } else {
          defaultValue = [];
        }
      }
      iModelValue[props.name] = defaultValue;
    }, {
      immediate: true,
    });

    watch(() => iModelValue[props.name], (val) => {
      ctx.emit('update:modelValue', val);
    });
    return {
      path: ctx.attrs.path,
      ...useDerivedProps(props),
      iOptions,
      iModelValue,
      state,
      setRef,
      handleClick,
    };
  },
});
</script>
