<template>
  <div
    v-loading="loading"
    :class="['i-qrcode__container', classname]"
  >
    <div ref="qrcode" />
  </div>
</template>
<script>
import {defineComponent, onMounted, ref, getCurrentInstance} from 'vue';
import {appendScript} from '../../utils/appendAssets';
import mixinProps from '../props/qrcode';

export default defineComponent({
  name: 'Qrcode',
  mixins: [mixinProps],
  setup(props) {
    const {proxy} = getCurrentInstance();
    const loading = ref(false);
    const init = () => {
      appendScript('qrcode')
        .then(() => {
        new window.QRCode(proxy.$.refs.qrcode, {
          width: props.size,
          height: props.size,
          text: props.value,
          colorDark: props.colorDark,
          colorLight: props.colorLight,
        });
      }).catch(e => {
        proxy.$message({
          message: e.message,
          showClose: true,
          grouping: true,
          type: 'error',
        });
        console.error(e);
      }).finally(() => {
        loading.value = false;
      });
    };

    onMounted(() => {
      loading.value = true;
      init();
    });

    return {
      loading
    };
  },
});
</script>
