<template>
  <div class="i-crud__container">
    <div
      v-if="showDynamicColumn || showExport || actions || title"
      :class="{border: border, 'i-crud__container__header': true}"
    >
      <div class="i-crud__container__title">
        {{ title }}
      </div>
      <div class="i-crud__container__tools">
        <i-component
          v-for="(item, index) in actions"
          :key="`${path}/actions/${index}`"
          :path="`${path}/actions/${index}/${item.renderer}`"
          :renderer="item.renderer"
          :props="iAttrs(item)"
          :header="iHeader(item)"
          :body="iBody(item)"
          :footer="iFooter(item)"
          :init-data="{selectedRows}"
          :action="action"
          :after-action="handleRowsUpdate"
        />
        <el-button
          v-if="showExport"
          class="i-component__fragment"
          size="small"
          @click="onExportExcel"
        >
          <i-icon :path="`${path}/download`" icon="Download" size="14" />&nbsp;
        </el-button>
        <el-dropdown
          v-if="showDynamicColumn"
          class="i-component__fragment"
          size="small"
          trigger="click"
          split-button
        >
          <i-icon :path="`${path}/columns`" icon="Filter" size="10" />
          <template #dropdown>
            <el-dropdown-menu>
              <el-checkbox-group v-model="dynamicColumn">
                <template
                  v-for="(column, index) in columns"
                  :key="index"
                >
                  <el-dropdown-item v-if="column.name">
                    <el-checkbox :label="column.name">
                      {{ column.label }}
                    </el-checkbox>
                  </el-dropdown-item>
                </template>
              </el-checkbox-group>
            </el-dropdown-menu>
          </template>
        </el-dropdown>
      </div>
    </div>
    <el-table
      v-loading="loading"
      ref="componentDom"
      :data="rows"
      :height="height"
      :stripe="stripe"
      :border="border"
      :fit="fit"
      :max-height="maxHeight"
      :show-header="showHeader"
      :span-method="onMergeCell"
      @selection-change="onSelectionChange"
      @sort-change="onSortChange"
    >
      <template
        v-for="(column, index) in columns"
        :key="`${path}/columns/${index}`"
      >
        <template v-if="dynamicColumn.includes(column.name)&&showColumnOn(column)">
          <el-table-column
            :prop="column.name"
            :label="column.label"
            :fixed="column.fixed"
            :align="column.align"
            :width="column.width"
            :type="column.type"
            :sortable="column.sortable"
            :selectable="selectableOn"
          >
            <template v-if="!column.type" #default="scope">
              <template v-if="scope.$index >= 0">
                <template v-if="$isArray(column.body)&&column.body.length">
                  <i-component
                    v-for="(item, count) in column.body"
                    :key="`${path}/rows/${scope.$index}/columns/${index}/body/${count}`"
                    :path="`${path}/rows/${scope.$index}/columns/${index}/body/${count}`"
                    :track="`${path}/columns/${index}/body/${count}`"
                    :renderer="item.renderer"
                    :header="iHeader(item)"
                    :body="iBody(item)"
                    :footer="iFooter(item)"
                    :init-data="{...state.data, ...scope.row}"
                    :props="iAttrs(item)"
                  />
                </template>
                <i-component
                  v-else-if="$isObject(column.body)"
                  :path="`${path}/rows/${scope.$index}/columns/${index}`"
                  :key="`${path}/rows/${scope.$index}/columns/${index}`"
                  :track="`${path}/columns/${index}/body`"
                  :renderer="column.body.renderer"
                  :header="iHeader(column.body)"
                  :body="iBody(column.body)"
                  :footer="iFooter(column.body)"
                  :init-data="{...state.data, ...scope.row}"
                  :props="iAttrs(column.body)"
                />
                <template v-else-if="$isString(column.body)">
                  {{$renderTpl(column.body, {...state.data, ...scope.row})}}
                </template>
                <template v-else>
                  {{scope.row[scope.column.property]}}
                </template>
              </template>
            </template>
          </el-table-column>
        </template>
      </template>
    </el-table>
    <el-pagination
      v-if="hasPageInfo && rows.length"
      v-model:page-size="pageInfo.pageSize"
      v-model:current-page="pageInfo.pageIndex"
      :layout="pageInfo.layout"
      :total="pageInfo.total"
      background
      small
      class="i-crud__container__pagination"
    />
  </div>
</template>
<script>
import {defineComponent, getCurrentInstance, onMounted, ref, watch} from 'vue';
import {cloneDeep} from 'lodash';
import {appendScript} from '../../utils/appendAssets';
import useLinkage from '../../hooks/useLinkage';
import useDerivedProps from '../../hooks/useDerivedProps';
import usePageInfo from '../../hooks/usePageInfo';
import useInitApi from '../../hooks/useInitApi';
import useInitData from '../../hooks/useInitData';
import useVisible from '../../hooks/useVisible';
import visible from '../props/visible';
import initData from '../props/init-data';
import initApi from '../props/init-api';
import mixinProps from '../props/table';

export default defineComponent({
  name: 'Table',
  mixins: [mixinProps, visible, initApi, initData],
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const { handleFetchApi, shouldComponentUpdate } = useInitApi(props);
    const state = useInitData(props);
    const pageInfo = usePageInfo(props);
    const iColumn = props.columns.map((item) => item.name);
    const selectedRows = ref([]);
    const rows = ref([]);
    const dynamicColumn = ref(iColumn);
    const loading = ref(false);
    const sortable = {};
    const onSelectionChange = (val) => {
      selectedRows.value = val;
    };
    const onSortChange = ({prop, order}) => {
      sortable.orderBy = prop;
      sortable.orderDir = order;
      pageInfo.pageIndex = 1;
      fetchDataList();
    };
    const onExportExcel = () => {
      loading.value = true;
      Promise.all([
        appendScript('xlsx'),
        appendScript('filesaver')
      ])
        .then(() => {
          const {XLSX, saveAs} = window;
          const table = proxy.$.refs.componentDom.$el;
          const fixedTable = table.querySelector('.el-table__fixed');
          const tableName = `${props.title || 'sheetjs'}.xlsx`;
          const wb = XLSX.utils.table_to_book(fixedTable || table, {raw: true});
          const wbout = XLSX.write(wb, {
            bookType: 'xlsx',
            type: 'array',
            bookSST: true,
          });
          saveAs(
            new Blob([wbout], { type: 'application/octet-stream' }),
            tableName
          );
        })
        .catch(e => {
          proxy.$message({
            message: e.message,
            showClose: true,
            grouping: true,
            type: 'error',
          });
          console.error(e);
        })
        .finally(() => {
          loading.value = false;
        });
    };
    const onComponentUpdate = () => {
      if (props.initApi) {
        fetchDataList();
      }
    };
    const fetchDataList = () => {
      if (proxy && proxy.axiosCancelToken) {
        typeof proxy.axiosCancelToken === 'function' && proxy.axiosCancelToken('CANCELED_DUE_TO_NEW_REQUEST');
      }
      const apiData = cloneDeep(props.initApi);
      loading.value = true;
      if (props.hasPageInfo && !apiData.params) {
        apiData.params = {};
      }
      if (sortable.orderBy) {
        apiData.params.orderBy = sortable.orderBy;
        apiData.params.orderDir = sortable.orderDir;
      }
      apiData.params[props.pageConfig.pageIndex || 'pageIndex'] = pageInfo.pageIndex;
      apiData.params[props.pageConfig.pageSize || 'pageSize'] = pageInfo.pageSize;
      handleFetchApi(apiData, state.data)
        .then((result) => {
          rows.value = result[props.pageConfig.rows || 'rows'];
          pageInfo.total = result[props.pageConfig.total || 'total'];
          loading.value = false;
        })
        .catch(err => {
          loading.value = false;
          if (err&&err.message === 'CANCELED_DUE_TO_NEW_REQUEST') {
            console.info(err);
          }
        });
    };
    const onMergeCell = (context) => {
      if (props.mergeCell) {
        for (const exp in props.mergeCell) {
          if (Object.hasOwnProperty.call(props.mergeCell, exp)) {
            if (proxy.$expressionEval(exp, context)) {
              return props.mergeCell[exp];
            }
          }
        }
      }
    };
    const selectableOn = (row) => {
      let selectable = true;
      if (props.selectableOn) {
        selectable = proxy.$expressionEval(props.selectableOn, row);
      }
      return selectable;
    };
    const showColumnOn = (column) => {
      let visible = true;
      if (column.visibleOn) {
        visible = proxy.$expressionEval(column.visibleOn, proxy.state.data);
      }
      return visible;
    };
    const handleRowUpdate = (res) => {
      let rowIndex = -1;
      for (let index = 0; index < rows.value.length; index++) {
        if (String(rows.value[index][props.updateOnRowId]) === String(res[props.updateOnRowId])) {
          rowIndex = index;
          break;
        }
      }
      if (rowIndex !== -1) {
        Object.assign(rows.value[rowIndex], res);
      }
      return rowIndex;
    };
    const handleRowsUpdate = (res) => {
      if (res.selectedRows) {
        const updatedRows = {};
        res.selectedRows.forEach(item => {
          const id = item[props.updateOnRowId];
          updatedRows[id] = item;
        });
        rows.value = rows.value.map(item => {
          const id = item[props.updateOnRowId];
          if (updatedRows[id]) {
            Object.assign(item, updatedRows[id])
          }
          return item;
        });
      }
    };
    const onRowUpdate = (res) => {
      let rowIndex = -1;
      if (res.selectedRows) {
        handleRowsUpdate(res);
        rowIndex = 1;
      } else {
        rowIndex = handleRowUpdate(res);
      }
      return rowIndex;
    };
    watch(() => props.initRows, (val) => {
      if (val && val.length) {
        rows.value = val;
      }
    }, {
      immediate: true,
    });
    onMounted(() => {
      onComponentUpdate();
    });
    return {
      path: ctx.attrs.path,
      ...useLinkage(props),
      ...useDerivedProps(props),
      ...useVisible(props),
      shouldComponentUpdate,
      onComponentUpdate,
      onSelectionChange,
      onExportExcel,
      onSortChange,
      onMergeCell,
      selectableOn,
      onRowUpdate,
      handleRowsUpdate,
      showColumnOn,
      fetchDataList,
      dynamicColumn,
      selectedRows,
      pageInfo,
      loading,
      state,
      rows
    };
  },
});
</script>
