<template>
  <div
    v-if="isMounted"
    :class="{
      'i-form__combo': true,
      [classname]: classname,
      [type]: type,
      inline
    }"
  >
    <template v-if="type === 'multiple'">
      <div
        v-if="iModelValue[name]"
        v-for="(comboItem, comboItemIndex) in iModelValue[name]"
        :key="`${path}/combo/${comboItem.name}`"
        class="i-form__combo__item"
      >
        <span class="i-form__combo__item__index">
          {{ comboItemIndex + 1 }}
        </span>
        <span
          v-if="editable"
          class="i-form__combo__item__name"
        >
          {{ comboItem.name }}
        </span>
        <div
          v-if="sortable"
          class="i-form__combo__item__sortable"
        >
          <i-icon
            :path="`${path}/${comboItemIndex}/up`"
            @click="onTop(comboItemIndex)"
            size="12"
            icon="Top"
            class="i-form__combo__item__up"
          />
          <i-icon
            :path="`${path}/${comboItemIndex}/down`"
            @click="onBottom(comboItemIndex)"
            size="12"
            icon="Bottom"
            class="bottom i-form__combo__item__down"
          />
        </div>
        <i-field
          v-for="(item, index) in controls"
          v-model="iModelValue[name][comboItemIndex][item.name]"
          :key="`${path}/controls/${item.name}`"
          :path="`${path}/controls/${index}`"
          :name="item.name"
          :props="getField(index, item, comboItemIndex)"
          :label-width="labelWidth"
          :hidden-on="item.hiddenOn"
          :visible-on="item.visibleOn"
          :disabled-on="item.disabledOn"
          :init-data="state.data"
          :field-blur="fieldBlur"
        />
        <span
          v-if="editable"
          class="i-form__combo__edit"
          @click.stop.prevent="onEdit(comboItemIndex)"
        >
          <i-icon :path="`${path}/${comboItemIndex}/edit`" class="el-icon-edit" icon="Edit" size="12"/>
        </span>
        <span
          class="i-form__combo__delete"
          @click="onDelete(comboItemIndex)"
        >
          <i-icon :path="`${path}/multiple/${comboItemIndex}/delete`" class="el-icon-delete" icon="Delete" size="12"/>
        </span>
      </div>
    </template>
    <template v-else-if="type === 'map'">
      <div
        v-for="(item, index) in iModelValue[name]"
        :key="`${path}/controls/${index}`"
        class="i-form__combo__item"
      >
        <i-field
          v-model="iModelValue[name][index].key"
          :path="`${path}/controls/${index}`"
          :props="getField(0)"
          :label-width="labelWidth"
          :init-data="state.data"
          :field-blur="fieldBlur"
        />
        <i-field
          v-model="iModelValue[name][index].value"
          :path="`${path}/controls/${index}`"
          :props="getField(1)"
          :label-width="labelWidth"
          :init-data="state.data"
          :field-blur="fieldBlur"
        />
        <span
          class="i-form__combo__delete"
          @click="onDelete(index)"
        >
          <i-icon :path="`${path}/map/${index}/delete`" class="el-icon-delete" icon="Delete" size="12"/>
        </span>
      </div>
    </template>
    <template v-else-if="type === 'array'">
      <div
        v-for="(item, index) in iModelValue[name]"
        :key="`${path}/controls/${index}`"
        class="i-form__combo__item border-less"
      >
        <i-field
          v-model="iModelValue[name][index]"
          :name="`${index}`"
          :path="`${path}/controls/${index}`"
          :props="getField(index)"
          :label-width="labelWidth"
          :init-data="state.data"
          :field-blur="fieldBlur"
        />
        <span
            class="i-form__combo__delete"
            @click="onDelete(index)"
        >
          <i-icon :path="`${path}/array/${index}/delete`" class="el-icon-delete" icon="Delete" size="12"/>
        </span>
      </div>
    </template>
    <template v-else-if="type === 'custom'">
      <div
        v-for="(item, comboItemIndex) in controls"
        :key="`${path}/combo/${comboItemIndex}`"
        class="i-form__combo__item"
      >
        <i-field
          v-model="iModelValue[name][item.name]"
          :key="`${path}/controls/${item.name}`"
          :path="`${path}/controls/${comboItemIndex}`"
          :name="item.name"
          :props="getField(comboItemIndex, item, comboItemIndex)"
          :label-width="labelWidth"
          :hidden-on="item.hiddenOn"
          :visible-on="item.visibleOn"
          :disabled-on="item.disabledOn"
          :init-data="state.data"
          :field-blur="fieldBlur"
        />
      </div>
    </template>
    <el-form-item
      v-if="!(iMax>=length) && type !== 'custom'"
      :label="' '"
      label-width="0px"
      class="i-form__combo__plus"
    >
      <el-button
        size="small"
        type="primary"
        plain
        icon="Plus"
        @click="onAdd"
      />
    </el-form-item>
  </div>
</template>
<script>
import {defineComponent, watch, reactive, ref, onMounted, getCurrentInstance} from 'vue';
import {cloneDeep} from 'lodash';

import {createValid} from '../../../utils/valid';
import useDropTools from '../../../hooks/useDropTools';
import useInitApi from '../../../hooks/useInitApi';
import useInitData from '../../../hooks/useInitData';
import initData from '../../props/init-data';
import mixinProps from '../../props/form/combo';

const mapControl = [
  {
    renderer: 'input',
    name: 'key',
    clearable: false
  },
  {
    renderer: 'input',
    name: 'value',
    clearable: false
  }
];
export default defineComponent({
  name: 'Combo',
  mixins: [mixinProps, initData],
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const { handleFetchApi } = useInitApi(props);
    const { swapOneInJSON } = useDropTools(props);
    const state = useInitData(props);
    const iModelValue = reactive({});
    const iMax = ref(0);
    const isMounted = ref(false);
    const onAdaptor = () => {
      let modelValue = props.modelValue || props.defaultValue;
      if (!modelValue) {
        modelValue = props.controls.reduce((total, item) => {
          total[item.name] = item.defaultValue;
          return total;
        }, {});
      }
      if (props.type === 'custom') {
        iModelValue[props.name] = modelValue;
      }
      if (props.type === 'map') {
        iModelValue[props.name] = Object.keys(modelValue).map(key => ({key, value: modelValue[key]}));
      }
      if ('multiple,array'.includes(props.type) && modelValue) {
        const temp = cloneDeep(modelValue || []);
        if (proxy.$isObject(temp)) {
          iModelValue[props.name] = [temp];
        } else if (proxy.$isArray(temp)) {
          iModelValue[props.name] = temp;
        } else {
          iModelValue[props.name] = [temp];
        }
      }
      iMax.value = Object.keys(iModelValue[props.name]).length;
    };
    const onAdd = () => {
      if (props.type === 'multiple') {
        const keyMap = props.controls.reduce((total, control) => {
          total[control.name] = control.defaultValue;
          return total;
        }, {});
        iModelValue[props.name].push(keyMap);
      } else if (props.type === 'array') {
        iModelValue[props.name].push('');
      } else {
        iModelValue[props.name].push({key: '', value: ''});
      }
      iMax.value = Object.keys(iModelValue[props.name]).length;
    };
    const onDelete = index => {
      iModelValue[props.name].splice(index, 1);
      iMax.value = Object.keys(iModelValue).length;
    };
    const getField = (index, item, comboItemIndex) => {
      if (props.type === 'map') {
        const control = mapControl[index];
        if (index === 0) {
          control.name = 'key';
          control.label = '';
        } else {
          control.name = 'value';
          control.label = '';
        }
        control.labelWidth = props.labelWidth;
        return control;
      }
      if (item) {
        let {visibleOn, hiddenOn, disabledOn, ...tempItem} = cloneDeep(item);
        let prop;
        if (!ctx.attrs.prop) {
          if (props.type === 'custom') {
            prop = `${props.name}.${props.controls[index].name}`;
          } else {
            prop = `${props.name}.${comboItemIndex}.${props.controls[index].name}`;
          }
        } else {
          if (props.type === 'multiple') {
            prop = `${ctx.attrs.prop}.${comboItemIndex}.${props.controls[index].name}`;
          } else if (props.type === 'custom') {
            prop = `${ctx.attrs.prop}.${props.controls[index].name}`;
          }
        }
        tempItem.labelWidth = props.labelWidth;
        tempItem.prop = prop;
        return tempItem;
      }
      if (props.type === 'array') {
        const control = cloneDeep(mapControl[0]);
        let prop;
        if (!ctx.attrs.prop) {
          prop = `${props.name}.${index}`;
        } else {
          prop = `${ctx.attrs.prop}.${index}`;
        }
        control.labelWidth = props.labelWidth;
        control.label = control.label ? `${control.label + (index + 1)}` : `${(index + 1)}`;
        control.name = `${props.name}.${index}`;
        control.prop = prop;
        if (props.controls && props.controls[0].rules) {
          control.rules = props.controls[0].rules;
          createValid(control, {[props.name]: iModelValue[props.name][index]}, proxy, prop);
        }
        return control;
      }
    };
    const onEdit = (index) => {
      proxy.$eventHub.$emit('component:attrs-editor', index, ctx.attrs.track);
    };
    const onTop = (index) => {
      swapOneInJSON(iModelValue[props.name], index, --index);
    };
    const onBottom = (index) => {
      swapOneInJSON(iModelValue[props.name], index, ++index);
    };
    watch(() => props.loading, (val, old) => {
      if (!val && old) {
        onAdaptor();
      }
    });
    watch(() => iModelValue[props.name], (val) => {
      let modelValue = {};
      if (props.type === 'map') {
        val && val.forEach(item => {
          modelValue[item.key] = item.value;
        });
      } else {
        modelValue = val;
      }
      ctx.emit('update:modelValue', modelValue);
    }, {
      deep: true
    });
    onMounted(() => {
      isMounted.value = true;
      onAdaptor();
    });

    return {
      path: ctx.attrs.path,
      handleFetchApi,
      getField,
      onDelete,
      onEdit,
      onAdd,
      onTop,
      onBottom,
      iModelValue,
      isMounted,
      state,
      iMax
    };
  },
});
</script>
