<template>
  <el-descriptions
    :title="title"
    :border="border"
    :direction="direction"
    :size="size"
    :column="column"
    class="i-renderer_form__static"
  >
    <template
      v-for="(control, index) in columns"
      :key="index"
    >
      <el-descriptions-item
        :label="control.label"
        :width="`${width}px`"
        :label-align="control.labelAlign"
        :align="control.align"
        :class-name="control.classname"
        :label-class-name="control.labelClassName"
      >
        <i-component
          v-if="$isObject(control.body)"
          :renderer="control.body.renderer"
          :path="`${path}/body/${control.body.renderer}`"
          :props="iAttrs(control.body)"
          :header="iHeader(control.body)"
          :body="iBody(control.body)"
          :footer="iFooter(control.body)"
          :init-data="state.data"
        />
        <template v-else>
          <span class="i-form__static__text">
           {{ state.data[control.name] }}
          </span>
        </template>
      </el-descriptions-item>
    </template>
  </el-descriptions>
</template>
<script>
import { defineComponent } from 'vue';

import useDerivedProps from '../../hooks/useDerivedProps';
import useInitData from '../../hooks/useInitData';
import mixinProps from '../props/static';
import initData from '../props/init-data';

export default defineComponent({
  name: 'Static',
  mixins: [mixinProps, initData],
  setup(props, ctx) {
    const state = useInitData(props);
    return {
      path: ctx.attrs.path,
      ...useDerivedProps(props),
      state,
    };
  },
});
</script>
