<template>
  <div
    v-if="iVisible"
    v-show="iHidden"
    :class="['i-image__container', classname]"
  >
    <el-image
      :src="src"
      :lazy="lazy"
      :fit="fit"
      teleported
      :preview-src-list="previews"
    >
      <template
        v-if="error"
        #error
      >
        {{ $renderTpl(error) }}
      </template>
      <template
        v-if="placeholder"
        #placeholder
      >
        {{ $renderTpl(placeholder) }}
      </template>
    </el-image>
  </div>
</template>
<script>
import { defineComponent, getCurrentInstance } from 'vue';
import useInitData from '../../hooks/useInitData';
import useVisible from '../../hooks/useVisible';
import initData from '../props/init-data';
import visible from '../props/visible';
import mixinProps from '../props/image';

export default defineComponent({
  name: 'Image',
  mixins: [mixinProps, initData, visible],
  setup(props) {
    const { proxy } = getCurrentInstance();
    const state = useInitData(props);
    const getSrc = (src) => proxy.$renderTpl(src, props.initData);

    return {
      ...useVisible(props),
      getSrc,
      state
    };
  },
});
</script>
