<template>
  <span
    v-if="iVisible"
    v-show="iHidden"
    :class="['i-component__fragment', classname]"
  >
    <el-popover
      v-if="popupType === 'over'"
      :title="popoverTitle"
      :content="popoverContent"
      :trigger="popoverTrigger"
      :placement="popoverPlacement"
      :popper-class="popperClass"
      :width="popperWidth"
    >
      <template #reference>
        <el-button
          v-if="type !== 'icon'"
          v-loading="loading"
          :class="classname"
          :size="size"
          :type="type"
          :plain="plain"
          :round="round"
          :circle="circle"
          :icon="icon"
          :disabled="loading || iDisabled"
        >
          {{ renderText }}
        </el-button>
        <i-icon
          v-else
          v-loading="loading"
          :path="`${path}/icon`"
          :icon="icon"
        />
      </template>
      <template v-if="$isArray(body)">
        <template
          v-for="(child, index) in body"
          :key="`${path}/${index}/${child.renderer}`"
        >
          <i-component
            :renderer="child.renderer"
            :path="`${path}/body/${index}/${child.renderer}`"
            :header="iHeader(child)"
            :body="iBody(child)"
            :footer="iFooter(child)"
            :props="iAttrs(child)"
            :init-data="state.data"
          />
        </template>
      </template>
      <i-component
        v-if="body && $isObject(body)"
        :renderer="body.renderer"
        :path="`${path}/${body.renderer}`"
        :body="iBody(body)"
        :header="iHeader(body)"
        :footer="iFooter(body)"
        :props="iAttrs(body)"
        :init-data="iData(body)"
      />
    </el-popover>
    <el-popconfirm
      v-else-if="popupType === 'confirm'"
      confirm-button-text=""
      cancel-button-text=""
      icon="InfoFilled"
      :title="confirmTitle"
      @confirm="onClick"
    >
      <template #reference>
        <el-button
          v-if="type !== 'icon'"
          v-loading="loading"
          :class="classname"
          :size="size"
          :type="type"
          :plain="plain"
          :round="round"
          :circle="circle"
          :icon="icon"
          :disabled="loading || iDisabled"
        >
          {{ renderText }}
        </el-button>
        <i-icon
          v-else
          v-loading="loading"
          :path="`${path}/icon`"
          :icon="icon"
        />
      </template>
    </el-popconfirm>
    <el-tooltip
      v-else-if="popupType === 'tip'"
      :disabled="tipDisabled"
      :content="tipContent"
      :placement="tipPlacement"
      :append-to-body="true"
    >
      <el-button
        v-if="type !== 'icon'"
        v-loading="loading"
        :size="size"
        :type="type"
        :plain="plain"
        :round="round"
        :circle="circle"
        :icon="icon"
        :disabled="loading"
        @click="onClick"
      >
        {{ text }}
      </el-button>
      <i-icon
        v-else
        v-loading="loading"
        :path="`${path}/icon`"
        :icon="icon"
        @click="onClick"
      />
    </el-tooltip>
    <el-badge
      v-else-if="popupType === 'badge'"
      :value="badgeText"
      :class="badgeClass"
    >
      <el-button
        v-if="type !== 'icon'"
        v-loading="loading"
        :class="classname"
        :size="size"
        :type="type"
        :plain="plain"
        :round="round"
        :circle="circle"
        :icon="icon"
        :disabled="loading || iDisabled"
        @click="onClick"
      >
        {{ text }}
      </el-button>
      <i-icon
        v-else
        v-loading="loading"
        :path="`${path}/icon`"
        :icon="icon"
        @click="onClick"
      />
    </el-badge>
    <el-button-group
      v-else-if="popupType === '' && actions"
      v-loading="loading"
      :class="classname"
    >
      <template
        v-for="(item, index) in actions"
        :key="`${path}/${index}`"
      >
        <el-button
          v-bind="item"
          :index="index"
          :disabled="loading || iDisabled"
          @click="onClick"
        >
          {{ item.text }}
        </el-button>
      </template>
    </el-button-group>
    <template v-else>
      <template v-if="type === 'image'">
        <el-avatar
          :class="['i-action__avatar', classname]"
          :shape="circle?'circle':'square'"
          :size="size"
          :src="icon"
          @click="onClick"
        />
        <span class="i-action__text">{{ renderText }}</span>
      </template>
      <i-icon
        v-else-if="type === 'icon'"
        :path="`${path}/icon`"
        :icon="icon"
        :size="size"
        @click="onClick"
      />
      <el-button
        v-else
        v-loading="loading"
        :class="classname"
        :size="size"
        :type="type"
        :plain="plain"
        :round="round"
        :circle="circle"
        :icon="icon"
        :disabled="iDisabled || loading"
        @click="onClick"
      >
        {{ renderText }}
      </el-button>
    </template>
  </span>
</template>
<script>
import {defineComponent, computed, getCurrentInstance, watch, nextTick, onBeforeUnmount, ref} from 'vue';

import useInitData from '../../hooks/useInitData';
import useDerivedProp from '../../hooks/useDerivedProps';
import useLinkage from '../../hooks/useLinkage';
import useVisible from '../../hooks/useVisible';
import visible from '../props/visible';
import initData from '../props/init-data';
import mixinProps from '../props/action';

export default defineComponent({
  name: 'Action',
  mixins: [mixinProps, initData, visible],
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const loading = ref(false);
    const state = useInitData(props);
    const { onLinkageTrigger } = useLinkage(props);
    const renderText = computed(() => proxy.$renderTpl(props.text, props.initData));
    const iDisabled = computed(() => proxy.$expressionEval(props.disabledOn, state.data));
    let activeIndex;

    const onClick = () => {
      loading.value = true;
      if (props.static) {
        return;
      }
      const { attributes } = event.currentTarget;
      let actionType = props.actionType;
      let remoteComponent;

      if (props?.actions?.length) {
        activeIndex = attributes.index && attributes.index.value;
        remoteComponent = attributes.remotecomponent && attributes.remotecomponent.value;
        actionType = props.actions[activeIndex].actionType;
      }
      if (remoteComponent || props.remoteComponent) {
        proxy.$eventHub.$emit(
          'component:remoteComponent',
          actionType,
          remoteComponent || props.remoteComponent,
          handleAfterAction,
        );
      } else if (typeof activeIndex === 'string') {
        props.action(
          {
            actions: true,
            path: ctx.attrs.path,
            ...props.actions[activeIndex],
          },
          props.initData,
          handleAfterAction,
        );
      } else {
        const {
          renderer, actionApi, header, body, footer,
        } = props;
        const { ...other } = ctx.attrs;
        props.action(
          {
            path: ctx.attrs.path,
            target: props.target,
            renderer,
            actionType,
            actionApi,
            header,
            body,
            footer,
            ...other,
          },
          props.initData,
          handleAfterAction,
        );
      }
    };

    const handleAfterAction = (data) => {
      loading.value = false;
      if (data === 'CANCEL_LOADING') {
        return;
      }
      if (props.actionApi && props.actionApi.method === 'put') {
        Object.assign(state.data, data);
      } else {
        state.data = data;
      }
      if (props.afterAction && typeof props.afterAction === 'function') {
        props.afterAction();
      }
      if (props.redirect) {
        proxy.$parent.filterAction({actionType: 'url', url: props.redirect}, state.data);
      }
      if (props.reload) {
        return proxy.$eventHub.$emit('component:reload', props.reload, state.data);
      }
      if (props.target) {
        onLinkageTrigger(props.target, state.data);
      }
    };
    watch(() => props.popupType, val => {
      if (proxy.$iRenderConfig.editable && val === 'over') {
        nextTick(() => {
          proxy.$eventHub.$emit('portal:show', 'wrapper', `${ctx.attrs.track}/body`, props.body);
        });
      }
    }, {
      immediate: true
    });
    watch(() => props.actionType, val => {
      if (proxy.$iRenderConfig.editable && ['dialog', 'drawer'].includes(val)) {
        nextTick(() => {
          proxy.$eventHub.$emit('portal:show', val, `${ctx.attrs.track}/body`, props.body);
        });
      }
    }, {
      immediate: true
    });
    onBeforeUnmount(() => {
      if (
        proxy.$iRenderConfig.editable &&
        (
          ['dialog', 'drawer'].includes(props.actionType) ||
          props.popupType === 'over'
        )) {
        nextTick(() => {
          proxy.$eventHub.$emit('portal:hidden', props.actionType, `${ctx.attrs.track}/body`, props.body);
        });
      }
    });

    return {
      ...useDerivedProp(props),
      ...useVisible(props),
      onClick,
      iDisabled,
      renderText,
      state,
      loading,
      path: ctx.attrs.path
    };
  },
});
</script>
