<template>
  <section class="i-todo-container">
    <header class="i-todo__header">
      <input
        class="i-todo__add"
        placeholder=""
        @keyup.enter="addTodo"
      />
      <span class="i-todo__checkbox" @click="checkAll()" />
    </header>
    <section v-if="todos.length" class="i-todo__body">
      <ul class="i-todo__body__list">
        <li
          v-for="(todo, index) in filteredTodos()"
          :key="index"
          :class="{completed: todo.done}"
        >
          <div class="i-todo__view">
            <input
              :checked="todo.done"
              type="checkbox"
              class="i-todo__view__toggle"
              @change="toggleTodo(todo, index)"
            />
            <label v-text="todo.title" />
            <button class="i-todo__view__delete" @click="deleteTodo(todo)" />
          </div>
        </li>
      </ul>
    </section>
    <el-empty v-else description="" />
    <footer v-show="todos.length" class="i-todo__footer">
      <span class="i-todo__count">
        <template v-if="visible === 'all'">
          <strong>{{filteredTodos().length}}</strong>
        </template>
        <template v-if="visible === 'active'">
          <strong>{{filteredTodos().length}}</strong>
        </template>
        <template v-if="visible === 'completed'">
          <strong>{{filteredTodos().length}}</strong>
        </template>
      </span>
      <ul class="i-todo__filter">
        <li
          v-for="(item, index) in [{key:'all', text: ''}, {key: 'active', text: ''}, {key: 'completed', text: ''}]"
          :key="index"
          :class="{selected: visible === item.key}"
          @click.prevent="visible = item.key"
        >
          {{item.text}}
        </li>
        <li v-loading="loading" @click="onSave"></li>
      </ul>
    </footer>
  </section>
</template>
<script>
import {defineComponent, getCurrentInstance, onMounted, ref} from 'vue';
import {cloneDeep} from 'lodash';

import useInitApi from '../../hooks/useInitApi';
import useInitData from '../../hooks/useInitData';
import initData from '../props/init-data';
import initApi from '../props/init-api';
import mixinProps from '../props/todo';

export default defineComponent({
  name: 'Todo',
  mixins: [mixinProps, initApi, initData],
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const { handleFetchApi, shouldComponentUpdate } = useInitApi(props);
    const state = useInitData(props);
    const todos = ref([]);
    const loading = ref(false);
    const visible = ref('all');
    const onComponentUpdate = () => {
      if (props.initApi) {
        fetchDataList();
      }
    };
    const fetchDataList = () => {
      if (proxy && proxy.axiosCancelToken) {
        typeof proxy.axiosCancelToken === 'function' && proxy.axiosCancelToken('CANCELED_DUE_TO_NEW_REQUEST');
      }
      const apiData = cloneDeep(props.initApi);
      loading.value = true;
      handleFetchApi(apiData, state.data)
        .then((result) => {
          todos.value = result;
        })
        .catch((err) => {
          if (err.message === 'CANCELED_DUE_TO_NEW_REQUEST') {
            console.info(err);
          }
        })
        .finally(() => {
          loading.value = false;
        });
    };
    const onSave = () => {
      if (loading.value) {
        return;
      }
      const {actionApi} = props;
      const { ...other } = ctx.attrs;
      loading.value = true;
      actionApi.params = '*';
      props.action(
        {
          actionType: 'ajax',
          actionApi,
          ...other,
        },
        todos.value,
        () => loading.value = false
      );
    };
    const checkAll = () => {
      todos.value.forEach(todo => {
        todo.done = true;
      });
    };
    const addTodo = (e) => {
      const title = e.target.value;
      if (title.trim()) {
        todos.value.push({
          title,
          done: false
        });
      }
      e.target.value = ''
    };
    const toggleTodo = (todo, index) => todos.value[index].done = !todo.done;
    const deleteTodo = (todo) => todos.value.splice(todos.value.indexOf(todo), 1);
    const doneEdit = (index) => todos.value[index].done = true;
    const filteredTodos = () => {
      if (visible.value === 'active') {
        return todos.value.filter(todo => !todo.done);
      }
      if (visible.value === 'completed') {
        return todos.value.filter(todo => todo.done);
      }
      return todos.value;
    };

    onMounted(() => {
      onComponentUpdate();
    });

    return {
      shouldComponentUpdate,
      onComponentUpdate,
      checkAll,
      addTodo,
      toggleTodo,
      deleteTodo,
      doneEdit,
      filteredTodos,
      onSave,
      loading,
      visible,
      todos,
      state
    };
  }
});
</script>
