<template>
  <div :class="['i-page__container', classname]">
    <template v-if="$isArray(body)">
      <template
        v-for="(child, index) in body"
        :key="`${path}/${index}/${child.renderer}`"
      >
        <i-component
          :renderer="child.renderer"
          :path="`${path}/body/${index}/${child.renderer}`"
          :props="iAttrs(child)"
          :header="iHeader(child)"
          :body="iBody(child)"
          :footer="iFooter(child)"
          :init-data="state.data"
        />
      </template>
    </template>
    <i-component
      v-else-if="$isObject(body)"
      :renderer="body.renderer"
      :path="`${path}/body/${body.renderer}`"
      :header="iHeader(body)"
      :body="iBody(body)"
      :footer="iFooter(body)"
      :props="iAttrs(body)"
      :init-data="state.data"
    />
    <template v-else-if="$isString(body)">
      {{$renderTpl(column.body, state.data)}}
    </template>
    <div
      v-if="iProtal"
      class="i-component__fragment"
    >
      <template
        v-for="({ actionType, body, data, visible }, track) in popMap"
        :key="track"
      >
        <component
          v-bind="body"
          :is="`i-${actionType}`"
          :path="track"
          :visible="visible"
          :init-data="data"
          :on-popup-invisible="destroyProtal"
          destroy-on-close
          lock-scroll
        />
      </template>
    </div>
    <section>
      <div
        v-for="({actionType, body}, path) in displayMap"
        :key="path"
        :renderer="actionType"
        :track="path"
        class="i-page-container__pop"
      >
        <i-wrapper
          v-if="body.header||body.body||body.footer"
          classname="i-toolmaker__pops__wrapper"
          :header="body.header"
          :body="body.body"
          :footer="body.footer"
          :path="path"
        />
        <i-wrapper
          v-else
          classname="i-toolmaker__pops__wrapper"
          :body="body"
          :path="path"
        />
      </div>
    </section>
    <el-backtop v-if="showBacktop" />
  </div>
</template>
<script>
import {defineComponent, getCurrentInstance, onBeforeMount, onBeforeUnmount, onMounted, reactive, ref} from 'vue';
import {getUrlParams} from '../../utils/url';
import {appendPageStyle, removePageStyle, appendWatermark} from '../../utils/tpl';
import useDerivedProps from '../../hooks/useDerivedProps';
import useInitData from '../../hooks/useInitData';
import useDriver from '../../hooks/useDriver';
import initData from '../props/init-data';
import mixinProps from '../props/page';
import driverProps from '../props/driver';

export default defineComponent({
  name: 'Page',
  mixins: [mixinProps, initData, driverProps],
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const { initDriver } = useDriver(props);
    const state = useInitData(props);
    const popMap = reactive({});
    const displayMap = ref({});
    const createProtal = (path, pop) => {
      popMap[path] = pop
    };
    const destroyProtal = (path) => { delete popMap[path]; };
    const showProtal = (actionType, path, body) => {
      if (!displayMap.value[path]) {
        displayMap.value[path] = {
          actionType,
          body
        };
      }
    };
    const hiddenProtal = (actionType, path, body) => {
      if (path) {
        delete displayMap.value[path];
      } else {
        displayMap.value = {};
      }
    };
    let watermarkDom;
    let id;

    onBeforeMount(() => {
      if (props.mergeUrlQuery) {
        const defaultQuery = getUrlParams();
        Object.assign(state.data, defaultQuery);
      }
    });
    onMounted(() => {
      if (props.iProtal) {
        proxy.$eventHub.$on('portal:create', createProtal);
        proxy.$eventHub.$on('portal:destroy', destroyProtal);
      }

      props.watermark && appendWatermark(proxy, props.watermark).then(res => watermarkDom = res);
      props.innerStyle && appendPageStyle(props.innerStyle).then(res => id = res);
      props.steps && props.steps.length && initDriver();

      proxy.$eventHub.$on('portal:show', showProtal);
      proxy.$eventHub.$on('portal:hidden', hiddenProtal);
    });
    onBeforeUnmount(() => {
      if (props.iProtal) {
        proxy.$eventHub.$off('portal:create', createProtal);
        proxy.$eventHub.$off('portal:destroy', destroyProtal);
      }

      watermarkDom && watermarkDom.destroy();
      props.innerStyle && removePageStyle(id);

      proxy.$eventHub.$off('portal:show', showProtal);
      proxy.$eventHub.$off('portal:hidden', hiddenProtal);
    });

    return {
      ...useDerivedProps(),
      destroyProtal,
      displayMap,
      popMap,
      state,
      path: ctx.attrs.path
    };
  },
});
</script>
