<template>
  <div class="i-component__fragment">
    <el-dialog
      v-model="iVisible"
      :title="renderTitle"
      :width="width"
      :fullscreen="fullscreen"
      :modal="modal"
      :lock-scroll="lockScroll"
      :close-on-click-modal="closeOnModal"
      :show-close="showClose"
      :destroy-on-close="destroyOnClose"
      :custom-class="`i-popup__container ${classname}`"
      @close="onClose"
    >
      <el-main
        v-if="body"
        class="i-popup__container__body"
      >
        <i-component
          v-if="$isObject(body)"
          :renderer="body.renderer"
          :path="`${path}/body/${body.renderer}`"
          :header="iHeader(body)"
          :body="iBody(body)"
          :footer="iFooter(body)"
          :props="iAttrs(body)"
          :init-data="state.data"
        />
        <template v-else>
          <template
            v-for="(item, index) in body"
            :key="`${path}/body/${index}/${item.renderer}`"
          >
            <i-component
              :renderer="item.renderer"
              :path="`${path}/body/${index}/${item.renderer}`"
              :header="iHeader(item)"
              :body="iBody(item)"
              :footer="iFooter(item)"
              :props="iAttrs(item)"
              :init-data="state.data"
            />
          </template>
        </template>
      </el-main>
      <template #footer>
        <div v-if="footer" class="i-popup__container__footer">
          <template v-if="$isArray(footer)">
            <template
              v-for="(item, index) in footer"
              :key="`${path}/footer/${index}/${item.renderer}`"
            >
              <i-component
                :path="`${path}/footer/${index}/${item.renderer}`"
                :renderer="item.renderer"
                :header="iHeader(item)"
                :body="iBody(item)"
                :footer="iFooter(item)"
                :props="iAttrs(item)"
                :init-data="state.data"
                :after-action="closable ? onClose : () => {}"
              />
            </template>
          </template>
          <i-component
            v-if="$isObject(footer)"
            :renderer="footer.renderer"
            :path="`${path}/footer/${footer.renderer}`"
            :header="iHeader(footer)"
            :body="iBody(footer)"
            :footer="iFooter(footer)"
            :props="iAttrs(footer)"
            :init-data="state.data"
            :after-action="closable ? onClose : () => {}"
          />
        </div>
      </template>
    </el-dialog>
  </div>
</template>
<script>
import {defineComponent, computed, ref, getCurrentInstance} from 'vue';

import useInitData from '../../hooks/useInitData';
import useDerivedProps from '../../hooks/useDerivedProps';
import initData from '../props/init-data';
import mixinProps from '../props/dialog';

export default defineComponent({
  name: 'Dialog',
  mixins: [mixinProps, initData],
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const state = useInitData(props);
    const iVisible = ref(props.visible);

    const onClose = () => {
      iVisible.value = false;
      props.onPopupInvisible && props.onPopupInvisible(ctx.attrs.path);
    };
    const renderTitle = computed(() => proxy.$renderTpl(props.title, props.initData));

    return {
      state,
      path: ctx.attrs.path,
      ...useDerivedProps(props),
      onClose,
      iVisible,
      renderTitle,
    };
  },
});
</script>
