<template>
  <el-form-item
    v-show="iHidden"
    v-if="iVisible"
    :label="props.label"
    :prop="props.prop || props.name"
    :required="requiredOn"
    :class="[
      props.className,
      {'i-form__formula': props.renderer === 'formula'}
    ]"
    :rules="rules"
    :label-width="props.labelWidth"
    :renderer="props.renderer"
  >
    <template
      v-if="props.label"
      #label
    >
      <span class="i-form__field">
        <span class="i-form__field__label">{{ props.label }}</span>
        <el-tooltip v-if="props.tip" :content="props.tip">
          <el-icon size="12">
            <component is="info-filled" />
          </el-icon>
        </el-tooltip>
      </span>
    </template>
    <el-link
      v-if="readOnly"
      disabled
    >
      {{state.data[props.name] || '-'}}
    </el-link>
    <template v-else>
      <component
        v-if="!error"
        v-bind="props"
        v-model="iModelValue[name]"
        :is="componentName"
        :path="path"
        :track="path"
        :name="props.name"
        :init-data="initData"
        :disabled="iDisabled"
        :label-width="props.labelWidth"
        :action="action"
        :field-blur="fieldBlur"
        :linkageTrigger="linkageTrigger"
      />
      <i-error v-else :error-info="errorInfo" :path="path" />
    </template>
  </el-form-item>
</template>

<script>
import {defineComponent, watch, reactive, getCurrentInstance, onMounted, onBeforeUnmount, onErrorCaptured, ref, computed} from 'vue';
import {cloneDeep} from 'lodash';
import useInitData from '../../../hooks/useInitData';
import useVisible from '../../../hooks/useVisible';
import initData from '../../props/init-data';
import visibleProps from '../../props/visible';
import mixinProps from '../../props/form/field';

export default defineComponent({
  name: 'Field',
  mixins: [mixinProps, visibleProps, initData],
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const state = useInitData(props);
    const { iVisible, iHidden, iDisabled } = useVisible(props, ctx);
    const iModelValue = reactive({});
    const error = ref('');
    const componentName = computed(() => {
      return error.value ? 'error' : `i-${props.props.renderer}`;
    });
    const errorInfo = computed(() => {
      if (error.value) {
        return {
          props: props.props,
          error: error.value,
        };
      }
      return {};
    });
    const requiredOn = computed(() => {
      if (props.props.requiredOn) {
        return proxy.$expressionEval(props.props.requiredOn, props.initData);
      }
      return false;
    });
    const handleVisible = (visible, name) => {
      if (props.name === name && props.handleInvisible) {
        props.handleInvisible(visible, name);
      }
    };
    const rules = computed(() => {
      return cloneDeep(props.props.rules);
    });

    watch(() => props.modelValue, (val) => {
      iModelValue[props.name] = val;
    }, {
      immediate: true,
    });

    watch(() => iModelValue[props.name], (val) => {
      ctx.emit('update:modelValue', val);
    });

    onErrorCaptured((err, vm, info) => {
      error.value = `'${err.message}' is found in ${props.name} renderer`;
      console.error(err, vm, info);
      return false;
    });

    onMounted(() => {
      proxy.$eventHub.$on('field:visible', handleVisible);
    });

    onBeforeUnmount(() => {
      proxy.$eventHub.$off('field:visible', handleVisible);
    });

    return {
      path: ctx.attrs.path,
      requiredOn,
      errorInfo,
      error,
      rules,
      componentName,
      iModelValue,
      iHidden,
      iVisible,
      iDisabled,
      state,
    };
  },
});
</script>
