<template>
  <el-header
    :height="`${iHeight}px`"
    :class="['i-header__container', classname, iComputedClass]"
  >
    <template v-if="$isArray(body)">
      <template
        v-for="(child, index) in body"
        :key="`${path}/${index}/${child.renderer}`"
      >
        <i-component
          :renderer="child.renderer"
          :path="`${path}/body/${index}/${child.renderer}`"
          :header="iHeader(child)"
          :body="iBody(child)"
          :footer="iFooter(child)"
          :props="iAttrs(child)"
          :init-data="state.data"
        />
      </template>
    </template>
    <i-component
      v-else-if="$isObject(body)"
      :renderer="body.renderer"
      :path="`${path}/body/${body.renderer}`"
      :header="iHeader(body)"
      :body="iBody(body)"
      :footer="iFooter(body)"
      :props="iAttrs(body)"
      :init-data="state.data"
    />
    <template v-else-if="$isString(body)">
      {{$renderTpl(body, state.data)}}
    </template>
  </el-header>
</template>

<script>
import {
  defineComponent, ref, computed, watch, getCurrentInstance,
} from 'vue';

import useInitData from '../../hooks/useInitData';
import useDerivedProps from '../../hooks/useDerivedProps';
import initData from '../props/init-data';
import mixinProps from '../props/header';

export default defineComponent({
  name: 'Header',
  mixins: [mixinProps, initData],
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const state = useInitData(props);
    const iHeight = ref('');
    const iComputedClass = computed(() => {
      if (props.computedClass) {
        return proxy.$expressionEval(props.computedClass, state.data);
      }
      return '';
    });

    watch(() => props.height, (val) => {
      if (val) {
        iHeight.value = val;
      }
    }, { immediate: true });

    return {
      path: ctx.attrs.path,
      ...useDerivedProps(),
      iComputedClass,
      iHeight,
      state,
    };
  },
});
</script>
