<template>
  <div
    v-loading="loading"
    class="i-crud__container"
  >
    <el-row :gutter="gutter">
      <el-col
        v-for="(item, index) in rows"
        :key="`${path}/${index}`"
        :span="span"
      >
        <i-card
          renderer="card"
          :path="path"
          :classname="classname"
          :shadow="shadow"
          :header="header"
          :body="body"
          :footer="footer"
          :actions="actions"
          :action="action"
          :init-data="item"
        />
      </el-col>
    </el-row>
    <el-pagination
      v-if="hasPageInfo && rows.length"
      v-model:page-size="pageInfo.pageSize"
      v-model:current-page="pageInfo.pageIndex"
      :layout="pageInfo.layout"
      :total="pageInfo.total"
      background
      small
      hide-on-single-page
      class="i-crud__container__pagination"
    />
  </div>
</template>
<script>
import {defineComponent, reactive, getCurrentInstance, onMounted, ref} from 'vue';
import {cloneDeep} from 'lodash';
import useDerivedProps from '../../hooks/useDerivedProps';
import usePageInfo from '../../hooks/usePageInfo';
import useLinkage from '../../hooks/useLinkage';
import useInitApi from '../../hooks/useInitApi';
import useInitData from '../../hooks/useInitData';
import initData from '../props/init-data';
import initApi from '../props/init-api';
import mixinProps from '../props/cards';

export default defineComponent({
  name: 'Cards',
  mixins: [mixinProps, initData, initApi],
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const { handleFetchApi, shouldComponentUpdate } = useInitApi(props);
    const state = useInitData(props);
    const pageInfo = usePageInfo(props);
    const rows = ref([]);
    const loading = ref(true);
    const iBody = reactive(props.body);
    const renderHeader = () => proxy.$getRenderedTpl(props.header, state.data);

    const onPageSizeChange = (pageSize) => {
      pageInfo.pageSize = Number(pageSize);
      pageInfo.pageIndex = 1;
      fetchDataList();
    };
    const onPageIndexChange = (pageIndex) => {
      if (event.currentTarget.classList.value === 'el-pager') {
        pageInfo.pageIndex = Number(pageIndex);
      }
      fetchDataList();
    };

    const onComponentUpdate = () => {
      if (props.initApi) {
        fetchDataList();
      }
    };

    const fetchDataList = () => {
      const apiData = cloneDeep(props.initApi);
      loading.value = true;
      if (!apiData.params) {
        apiData.params = {};
      }
      apiData.params[props.pageConfig.pageIndex || 'pageIndex'] = pageInfo.pageIndex;
      apiData.params[props.pageConfig.pageSize || 'pageSize'] = pageInfo.pageSize;
      handleFetchApi(apiData, state.data)
        .then((result) => {
          rows.value = result[props.pageConfig.rows || 'rows'];
          pageInfo.total = result[props.pageConfig.total || 'total'];
        })
        .finally(() => {
          loading.value = false;
        });
    };

    onMounted(() => {
      onComponentUpdate();
    });

    return {
      path: ctx.attrs.path,
      ...useDerivedProps(props),
      ...useLinkage(props),
      shouldComponentUpdate,
      onComponentUpdate,
      onPageSizeChange,
      onPageIndexChange,
      renderHeader,
      fetchDataList,
      iBody,
      pageInfo,
      loading,
      state,
      rows
    };
  },
});
</script>
