<template>
  <el-menu-item-group>
    <template
      v-if="title"
      #title
    >
      {{ title }}
    </template>
    <template
      v-for="(item, count) in body"
      :key="count"
    >
      <i-component
        :path="`${path}/${count}/${item.renderer}`"
        :renderer="item.renderer"
        :name="item.name"
        :props="item"
        :header="iHeader(item)"
        :body="iBody(item)"
        :footer="iFooter(item)"
        :index="index"
      />
    </template>
  </el-menu-item-group>
</template>

<script>
import { defineComponent } from 'vue';

import useDerivedProps from '../../../hooks/useDerivedProps';

import mixinProps from '../../props/menu/menu-item-group';

export default defineComponent({
  name: 'MenuItemGroup',
  mixins: [mixinProps],
  setup(props, ctx) {
    return {
      path: ctx.attrs.path,
      ...useDerivedProps(props),
    };
  },
});
</script>
